<template>
    <div class="container">
        <div class="header">{{title}} <el-button class="back-Btn" size="small" type="primary" icon="el-icon-back" @click="back">返回</el-button> </div>
        <el-form label-position="left" label-width="90px" :model="form">
            <el-form-item v-if="edit" required label="上传视频">
                <el-upload
                        action="/uploadFile.html?action=uploadFile"
                        :file-list="this.form.videoList"
                        :limit="1"
                        :disabled="Boolean(edit && this.form.videoList[0])"
                        :before-upload="beforeUpload"
                        :on-success="uploadSuccess"
                        :on-error="uploadError"
                        :show-file-list="false"
                        list-type="text">
                    <el-button size="small" :disabled="Boolean(edit && this.form.videoList[0])" type="primary">点击上传</el-button>
                    <div slot="tip">
                        <div>视频时长不能超过5分钟，大小不能超过25MB，仅支持mp4格式</div>
                        <div>建议在上传视频之前进行压缩。压缩视频网址： <a href="https://www.shipinyasuo.com" target="_blank" class="blue-text">www.shipinyasuo.com</a></div>
                    </div>
                </el-upload>
                <template v-if="this.form.videoList[0]">
                    <div class="video">
                        <div class="video">
                            <video ref="video" class="video-max" controls preload="auto" @loadedmetadata="loadedMetaData" :src="this.form.videoList[0].url" alt="" />
                        </div>
                        <div class="video-btn">
                            <el-button class="video-delete" type="danger" icon="el-icon-delete" circle @click="deleteVideo"></el-button>
                        </div>
                    </div>
                </template>
            </el-form-item>
            <el-form-item v-else required label="视频">
                <template v-if="this.form.videoList[0]">
                    <div class="video">
                        <div class="video">
                            <video class="video-max" controls preload="auto" :src="this.form.videoList[0].url" alt="" />
                        </div>
                    </div>
                </template>
            </el-form-item>
            <titleInput :form="form" :edit="!edit"></titleInput>
            <el-form-item label="内容标签">
                <tagList v-if="edit" :labelList="this.form.tagList"></tagList>
                <ft-labels v-else :labels="this.form.tagList"></ft-labels>
            </el-form-item>
            <el-form-item v-if="edit">
                <el-button type="primary" @click="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import tagList from '../components/tagList';
import titleInput from '../components/titleInput';

const VIDEO_SIZE_THRESHOLD = 25 * 1024 * 1024;

export default {
    name: 'videoEdit',
    components: {
        tagList,
        titleInput
    },
    data() {
        return {
            title: '',
            form: this.createEmptyData()
        };
    },
    created() {
        this.checkPageStatus();
    },
    methods: {
        checkPageStatus() {
            if (this.$route.query.id) {
                // action为edit，同时id不为空
                // 此时为 查看页面 不可编辑
                this.edit = this.$route.query.action === 'edit';
                this.title = this.edit ? '编辑视频' : '查看视频';
                this.id = this.$route.query.id;
                this.findDataById(this.id);
            } else {
                // 此时为 新增页面
                this.title = '添加视频';
                this.edit = true;
                this.id = 0;
                this.resetData();
            }
        },
        createEmptyData() {
            return {
                video: '',
                videoList: [],
                title: '',
                tagList: [],
            };
        },
        resetData() {
            this.form = this.createEmptyData();
        },
        findDataById(resourceId) {
            this.$loading({
                text: '获取数据中...'
            });
            return this.$get('/resources/findResourcesById.json', { resourceId }).then((res) => {
                if (res.data.retcode === 200) {
                    const data = res.data.object;
                    this.form.title = data.resource.title;
                    this.form.duration = Number(data.resource.vedioLength);
                    this.form.videoList = [{ url: data.resource.vedioUrl }];
                    data.labels.forEach((value) => {
                        value.closable = true;
                    });
                    this.form.tagList = data.labels;
                } else {
                    this.form.pictureList = [];
                    this.$alert('获取数据失败');
                }
                return Promise.resolve(res);
            }).finally(() => {
                this.$loading().close();
            });
        },
        beforeUpload(file) {
            if (this.form.videoList.length === 1) {
                return false;
            }
            if (!/.mp4$/.test(file.name)) {
                this.$alert('文件格式必须是mp4');
                return false;
            }
            if (file.size > VIDEO_SIZE_THRESHOLD) {
                this.$alert('文件大小必须小于25M');
                return false;
            }
            return true;
        },
        uploadSuccess(res) {
            if (res.retcode === 200 && res.imageUrl) {
                this.$message.success('上传成功');
                this.form.duration = undefined;
                this.form.videoList = [{ url: res.imageUrl }];
            } else {
                this.$alert('上传失败');
            }
        },
        uploadError() {
            this.$alert('上传视频失败');
        },
        deleteVideo() {
            this.$confirm('是否删除视频？').then(() => {
                this.form.videoList = [];
            });
        },
        submit() {
            const params = {
                resourceType: 2,
                title: this.form.title,
                labels: this.form.tagList.map(label => label.labelId).join(','),
                vedioUrl: this.form.videoList[0] && this.form.videoList[0].url,
                vedioLength: Math.ceil(this.form.duration)
            };
            if (this.id) {
                params.resourceId = this.id;
            }
            if (params.title.length < 1) {
                this.$alert('请输入视频标题');
                return;
            }
            if (params.title.length > 30) {
                this.$alert('视频标题的长度超过了30,请适当减少');
                return;
            }
            if (!params.vedioUrl) {
                this.$alert('必须上传视频才能保存');
                return;
            }
            if (params.vedioLength === undefined) {
                this.$alert('未获取到视频时长');
                return;
            }
            if (params.vedioLength > 300) {
                this.$alert('视频时长必须小于5分钟');
                return;
            }
            this.$loading({
                text: '保存数据中...'
            });
            this.$post('/resources/addOrModifyResource.json', params).then(() => {
                this.$loading().close();
                this.$alert('保存成功').then(() => {
                    this.back();
                });
            }).finally(() => {
                this.$loading().close();
            });
        },
        back() {
            this.$router.push('/marketing/list?type=video');
        },
        loadedMetaData() {
            console.log('视屏时长', this.$refs.video.duration, '秒');
            this.form.duration = this.$refs.video.duration;
        }
    }

};
</script>

<style scoped>
    .back-Btn{
        float: right;
    }
    .video{
        display: flex;
        align-items: center;
        justify-content: start;
    }
    .video-btn{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .video-delete{
        height: 40px;
        margin-left: 20px;
    }
    .video-max{
        max-height: 300px;
        min-height: 300px;
        max-width: 500px;
        min-width: 300px;
    }
    .blue-text{
        color: blue;
    }
</style>
