                    <span>问卷标题及说明</span>
                </div>
                <div class="item-content">
                    <div class="item-list">
                        <span class="search-name">
                            问卷标题：
                        </span>
                        <div class="question-title">
                            <el-input size="small"
                                      maxlength="30"
                                      :disabled="!!questionId"
                                      v-model="params.name"></el-input>
                            <div class="num-show">
                                {{params.name.length}}/30
                            </div>
                        </div>
                    </div>
                    <div class="item-list">
                        <span class="search-name">
                            问卷说明页图片：
                        </span>
                        <el-upload class="avatar-uploader"
                                   :disabled="!!questionId"
                                   :class="noImg(params.descriptionImg)"
                                   action="/uploadImage.html?action=uploadImage"
                                   :show-file-list="false"
                                   :on-success="handleAvatarSuccess1"
                                   :before-upload="beforeAvatarUpload">
                            <img v-if="params.descriptionImg"
                                 :src="params.descriptionImg"
                                 class="avatar">
                            <i v-else
                               class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <div class="img-remark">
                            <el-checkbox v-model="params.showDesc"
                                         :disabled="!!questionId">不显示问卷说明页</el-checkbox><br>
                            <span class="font-red">建议尺寸750px*1137px，须150k以下</span>
                        </div>
                    </div>
                    <div class="item-list">
                        <span class="search-name">
                            背景图片：
                        </span>
                        <el-upload class="avatar-uploader"
                                   :class="noImg(params.backgroundImg)"
                                   action="/uploadImage.html?action=uploadImage"
                                   :show-file-list="false"
                                   :disabled="!!questionId"
                                   :on-success="handleAvatarSuccess2"
                                   :before-upload="beforeAvatarUpload">
                            <img v-if="params.backgroundImg"
                                 :src="params.backgroundImg"
                                 class="avatar">
                            <i v-else
                               class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <div class="img-remark">
                            <span class="font-red">建议尺寸750px*1137px，须150k以下</span>
                        </div>
                    </div>
                    <div class="item-list">
                        <span class="search-name font-red">
                            尾页图片：
                        </span>
                        <el-upload class="avatar-uploader"
                                   :class="noImg(params.tailImg)"
                                   action="/uploadImage.html?action=uploadImage"
                                   :show-file-list="false"
                                   :disabled="!!questionId"
                                   :on-success="handleAvatarSuccess3"
                                   :before-upload="beforeAvatarUpload">
                            <img v-if="params.tailImg"
                                 :src="params.tailImg"
                                 class="avatar">
                            <i v-else
                               class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        <div class="img-remark">
                            <span class="font-red">建议尺寸750px*1137px，须150k以下</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="item">
                <div class="item-title">
                    <span>问卷问题</span>
                </div>
                <div class="item-content padding0">
                    <br>
                    <div class="add-btn-box">
                        <el-button type="primary"
                                   @click="addQuestion(0)"
                                   v-if="!questionId"
                                   size="small">添加选择题</el-button>
                        <el-button type="primary"
                                   @click="addQuestion(2)"
                                   v-if="!questionId"
                                   size="small">添加输入框</el-button>
                    </div>
                    <div class="no-question"
                         v-if="!params.subjects.length">暂无问题，请点击上方选项添加</div>
                    <div class="question-item"
                         v-for="(item, index1) in params.subjects"
                         :key="index1">
                        <span>{{item.subjectName || '请输入问题'}}</span>
                        <span v-if="item.subjectType !== 2">（{{item.subjectType === 1 ? '多' : '单'}}选）</span>
                        <div class="answer-box"
                             v-if="item.subjectType !== 2">
                            <div class="answer-item"
                                 v-for="(answer, index2) in item.answers"
                                 :key="index2">
                                <div class="label-left">
                                    <el-checkbox disabled
                                                 v-if="item.subjectType === 1">{{answer.answerName || `选项${codeList[index2]}`}}</el-checkbox>
                                    <el-radio disabled
                                              v-if="item.subjectType === 0"
                                              :label="index2">{{answer.answerName || `选项${codeList[index2]}`}}</el-radio>
                                </div>
                                <div class="label-box">
                                    <div v-if="!questionId">
                                        <label-picker class="tag-chosen-field"
                                                      :searchFunc="searchFunc"
                                                      :labels="answer.labels"
                                                      :auto-search="Boolean(true)"
                                                      :refresh-after-hide="Boolean(true)"
                                                      @pickLabel="pickLabel"
                                                      @deleteLabel="deleteLabel">
                                        </label-picker>
                                    </div>
                                    <div v-else>
                                        <el-tag v-for="(label, index3) in answer.labels"
                                                :key="index3">{{label.labelName}}</el-tag>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="answer-box w50"
                             v-if="item.subjectType === 2">
                            <el-input type="textarea"
                                      disabled
                                      :autosize="{ minRows: 4, maxRows: 4}"
                                      resize="none"
                                      placeholder="请输入内容"
                                      v-model="radioList[index1]"
                                      maxlength="100">
                            </el-input>
                            <div class="num-show">
                                {{radioList[index1].length}}/100
                            </div>
                        </div>
                        <div class="edit-box"
                             v-if="!questionId">
                            <el-button type="success"
                                       size="small"
                                       @click="editTheQuestion(index1)">编辑</el-button>
                            <el-button type="warning"
                                       size="small"
                                       v-if="index1 !== 0"
                                       @click="upTheQuestion(index1)">上移</el-button>
                            <el-button type="warning"
                                       size="small"
                                       v-if="index1 !== params.subjects.length - 1"
                                       @click="downTheQuestion(index1)">下移</el-button>
                            <el-button type="danger"
                                       size="small"
                                       @click="deleteTheQuestion(index1)">删除</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <el-dialog :visible.sync="isEdit"
                   width="50%"
                   :title="`${dialogType}问题`">
            <div class="question-box"
                 v-if="isEdit">
                <div class="question-box-item">
                    <div class="question-box-item-title">
                        <span>问题</span>
                        <div class="choose-box"
                             v-if="editQuestion.subjectType !== 2">
                            <el-radio v-model="editQuestion.subjectType"
                                      label="0">单选</el-radio>
                            <el-radio v-model="editQuestion.subjectType"
                                      label="1">多选</el-radio>
                        </div>
                    </div>
                    <div class="question-box-item-content">
                        <el-input placeholder="请输入问题"
                                  v-model="editQuestion.subjectName"
                                  maxlength="30"
                                  size="small"></el-input>
                    </div>
                </div>
                <div class="question-box-item"
                     v-if="editQuestion.subjectType !== 2">
                    <div class="question-box-item-title">
                        <span>选项</span>
                    </div>
                    <div class="question-box-item-content">
                        <div class="answer-every"
                             v-for="(answer, index) in editQuestion.answers"
                             :key="index">
                            <div class="answer-input">
                                <el-input v-model="answer.answerName"
                                          maxlength="10"
                                          size="small"
                                          :placeholder="`请输入选项${codeList[index]}`"></el-input>
                            </div>
                            <el-button type="primary"
                                       size="small"
                                       @click="addOption(index)">增加选项</el-button>
                            <el-button v-if="index > 1"
                                       type="danger"
                                       size="small"
                                       @click="deleteOption(index)">删除选项</el-button>
                        </div>
                    </div>
                </div>
                <div class="save-add">
                    <el-button type="success"
                               size="small"
                               @click="saveOne()">保存</el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>

import labelPicker from '../../components/label-picker';

const IMAGE_SIZE_THRESHOLD = 150 * 1024;

const codeList = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J']; // 字母列表，仅用于页面展示
const radioList = ['', '', '', '', '', '', '', '', '', '']; // 单选model列表，仅用于页面展示

export default {
    name: 'questionDetail',
    components: { labelPicker },
    props: ['id'],
    data() {
        return {
            questionId: '',
            isEdit: false,
            codeList,
            radioList,
            params: {
                name: '',
                showDesc: false,
                descriptionImg: '',
                backgroundImg: '',
                tailImg: '',
                subjects: []
            },
            editQuestion: {},
            dialogType: '添加'
        };
    },
    created() {
        // 获取问卷id
        this.questionId = this.$route.query.id;
        if (this.questionId) this.getDetail();
    },
    methods: {
        getDetail() {
            this.$get('/question/viewQuestion.json', { questionId: this.questionId }).then((res) => {
                this.params = res.data.object;
            });
        },
        handleAvatarSuccess1(res) {
            if (res.retcode === 200) {
                this.$message.success('上传成功');
                this.params.descriptionImg = res.imageUrl;
            } else {
                this.$alert('上传失败');
            }
        },
        handleAvatarSuccess2(res) {
            if (res.retcode === 200) {
                this.$message.success('上传成功');
                this.params.backgroundImg = res.imageUrl;
            } else {
                this.$alert('上传失败');
            }
        },
        handleAvatarSuccess3(res) {
            if (res.retcode === 200) {
                this.$message.success('上传成功');
                this.params.tailImg = res.imageUrl;
            } else {
                this.$alert('上传失败');
            }
        },
        beforeAvatarUpload(e) {
            // el-upload组件上传前事件
            // 限制图片上传大小 < 150K
            if (e.size > IMAGE_SIZE_THRESHOLD) {
                this.$alert('上传图片不能大于150k');
                // 返回false则不执行上传
                return false;
            }
            return true;
        },
        noImg(imageUrl) {
            // 返回一个className，用于更改el-upload组件的样式（默认样式不生效）
            return imageUrl ? '' : 'noImg';
        },
        save() {
            // 保存问卷
            if (!this.params.name) return this.$message.error('请填写问卷标题');
            if (!this.params.descriptionImg && this.params.showDesc) return this.$message.error('请上传问卷说明页图片');
            if (!this.params.backgroundImg) return this.$message.error('请上传背景图片');
            if (!this.params.tailImg) return this.$message.error('请上传尾页图片');
            if (!this.params.subjects.length) return this.$message.error('请添加问题');
            this.sortSubjects();
            this.params.showDesc = this.params.showDesc ? 1 : 0;
            this.$post('/question/addQuestion.json', this.params).then(() => {
                this.$route.push('/question/list');
            });
        },
        back() {
            this.$confirm('数据未保存，是否确认离开页面', '提示', {
                showCancelButton: true,
                type: 'warning'
            }).then(() => {
                this.$router.back();
            });
        },
        addQuestion(type) {
            // 添加问题
            if (this.isEdit) return this.$message.error('请先保存');
            if (this.params.subjects.length === 10) return this.$message.error('问题最多为10个');
            this.dialogType = '添加';
            this.isEdit = true;
            const newQuestion = {
                subjectNumber: '',
                subjectName: '',
                subjectType: type,
                answers: [
                    {
                        answerNumber: '1',
                        answerName: '',
                        labels: []
                    },
                    {
                        answerNumber: '2',
                        answerName: '',
                        labels: []
                    },
                    {
                        answerNumber: '3',
                        answerName: '',
                        labels: []
                    }
                ]
            };
            newQuestion.subjectNumber = this.params.subjects.length + 1;
            this.editQuestion = newQuestion;
            return '';
        },
        saveOne() {
            // 保存问题
            let msg = '';
            const { editQuestion } = this;
            if (editQuestion.subjectType !== 2) {
                editQuestion.answers.forEach((item) => {
                    if (!item.answerName) msg = '选项不能为空';
                });
            }
            if (!editQuestion.subjectName) msg = '问题名称不能为空';
            if (msg) return this.$message.error(msg);

            // 每次保存时，需要清空每个选项的标签 TODO
            if (editQuestion && editQuestion.answers) {
                editQuestion.answers.forEach(answer => {
                    if (answer.labels && answer.labels.length) {
                        answer.labels = [];
                    }
                });
            }

            // 只有新题目才能添加到题目列表中
            if (this.params.subjects.indexOf(editQuestion) === -1) {
                this.params.subjects.push(editQuestion);
            }
            this.isEdit = false;
        },
        addOption(index) {
            if (this.editQuestion.answers.length === 10) return this.$message.error('选项最多为10个');
            const obj = {
                answerNumber: '',
                answerName: '',
                labels: []
            };
            this.editQuestion.answers.splice(index + 1, 0, obj);
            // 重新排序
            this.updataAnswerNum();
            return '';
        },
        deleteOption(index) {
            // 删除选项
            this.$confirm('是否确认删掉此选项', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.editQuestion.answers.splice(index, 1);
                // 重新排序
                this.updataAnswerNum();
            });
        },
        updataAnswerNum() {
            // 重新排序
            const { answers } = this.editQuestion;
            answers.forEach((item, index) => {
                item.answerNumber = index + 1;
            });
        },
        editTheQuestion(index) {
            // 编辑
            this.dialogType = '编辑';
            this.editQuestion = this.params.subjects[index];
            this.isEdit = true;
        },
        upTheQuestion(index) {
            // 上移
            this.params.subjects[index] = this.params.subjects.splice(index - 1, 1, this.params.subjects[index])[0];
        },
        downTheQuestion(index) {
            // 下移
            this.params.subjects[index] = this.params.subjects.splice(index + 1, 1, this.params.subjects[index])[0];
        },
        deleteTheQuestion(index) {
            // 删除问题
            this.$confirm('是否确认删掉此问题及选项', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.params.subjects.splice(index, 1);
            });
        },
        sortSubjects() {
            // 为每个问题设置subjectNumber
            const { subjects } = this.params;
            subjects.forEach((item, index) => {
                item.subjectNumber = index + 1;
            });
        },
        searchFunc(key) {
            const params = {
                labelPartitions: '2,3', // 2：内容标签，3：兼容标签
                labelPartition: 1, // 1表示用户标签
                labelGroupId: 0 // 暂时传0
            };
            if (key) {
                params.labelName = key;
            }
            return this.$get('/crm/label/search.json', params).then((res) => {
                const { object } = res.data;
                if (object && object.length) {
                    object.forEach((item) => {
                        item.closable = true;
                    });

                    return Promise.resolve(object);
                }
                return Promise.resolve([]);
            });
        },
        // 标签选择事件
        pickLabel(label, labels) {
            const index = labels.indexOf(label);
            if (index > -1) return this.$message.error('已选择此标签');
            labels.push(label);
            return '';
        },
        // 标签删除事件
        deleteLabel(label, labels) {
            const index = labels.indexOf(label);
            if (index > -1) labels.splice(index, 1);
        },
    }
};
</script>

