<template>
    <div class="home">
        <div class="header">
            标签管理
        </div>
        <div class="content">
            <div class="item">
                <div class="item-title">
                    <span>筛选</span>
                    <div class="btn-box">
                        <el-button @click="showScreen()"
                                   size="mini">{{screen ? '收起' : '展开'}}筛选</el-button>
                        <el-button @click="search()"
                                   type="success"
                                   size="mini">查询结果</el-button>
                    </div>
                </div>
                <div class="item-content"
                     v-show="screen">
                    <div class="search-box">
                        <div class="clear-box">
                            <el-button @click="clearSearch()"
                                       type="danger"
                                       size="mini">清除筛选</el-button>
                        </div>
                    </div>
                    <div class="item-list"
                         v-for="(item,index) in searchList"
                         :key="index"
                         :class="item.type==='time' || item.type==='input' ? 'item-block' : ''">
                        <span class="search-name"
                              v-if="item.type !== 'input'">
                            {{item.name}}：
                        </span>
                        <div class="search-content"
                             :class="item.type==='time' ? 'search-time' : item.type==='input' ? 'search-input' : ''">
                            <div v-if="item.type === 'select'">
                                <el-select v-model="showSearchData[item.key]"
                                           size="small"
                                           @change="selectChange(item)"
                                           :disabled="showSearchData.labelPartition === '1' && item.name === '标签分组'">
                                    <el-option v-for="(itemSelect, index2) in item.list"
                                               :key="index2"
                                               :label="itemSelect.name || itemSelect.labelGroupName"
                                               :value="itemSelect.value || itemSelect.labelGroupId">
                                    </el-option>
                                </el-select>
                            </div>
                            <!-- <div v-if="item.type === 'integer' || item.type === 'decimal'">
                                <div class="search-input">
                                    <el-input size="small"
                                              type="number"
                                              placeholder="请输入"
                                              v-model="showSearchData[item.key+'Min']"></el-input>
                                </div>
                                至
                                <div class="search-input">
                                    <el-input size="small"
                                              type="number"
                                              placeholder="请输入"
                                              v-model="showSearchData[item.key+'Max']"></el-input>
                                </div>
                                <span class="clear"
                                      @click="clear(index)">清除</span>
                            </div> -->
                            <div v-if="item.type==='time'">
                                <el-date-picker v-model="showSearchData[`${item.key}Min`]"
                                                type="datetime"
                                                size="small"
                                                value-format="yyyy-MM-dd HH:mm:ss"
                                                placeholder="选择开始日期时间">
                                </el-date-picker>
                                至
                                <el-date-picker v-model="showSearchData[`${item.key}Max`]"
                                                type="datetime"
                                                size="small"
                                                value-format="yyyy-MM-dd HH:mm:ss"
                                                placeholder="选择结束日期时间">
                                </el-date-picker>
                                <span class="clear"
                                      @click="clear(index)">清除</span>
                            </div>
                            <div v-if="item.type === 'input'">
                                <el-input size="small"
                                          v-model="showSearchData[item.key]"
                                          :placeholder="item.placeholder"></el-input>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="item">
                <div class="item-title">
                    <span>数据列表</span>
                    <div class="btn-box">
                        <el-button @click="addNew()"
                                   size="mini">新建标签</el-button>
                    </div>
                </div>
                <div class="item-content padding0">
                    <ft-table ref="myTable"
                              border
                              tooltip-effec="dark"
                              :data="dataList"
                              :has-item="hasItem"
                              :equal-item="equalItem"
                              @pickListChange="pickListChange">
                        <el-table-column type="selection"
                                         width="55">
                        </el-table-column>
                        <el-table-column prop="labelId"
                                         label="标签编号">
                        </el-table-column>
                        <el-table-column prop="labelName"
                                         label="标签名称">
                        </el-table-column>
                        <el-table-column prop="labelPartitionName"
                                         label="标签大类">
                        </el-table-column>
                        <el-table-column prop="labelGroupName"
                                         label="标签分组">
                        </el-table-column>
                        <el-table-column prop="labelTypeName"
                                         label="标签类型">
                        </el-table-column>
                        <el-table-column prop="isUserShowName"
                                         label="标签展示类型">
                        </el-table-column>
                        <el-table-column label="创建时间">
                            <template slot-scope="scope">
                                {{returnDate(scope.row.createTime)}}
                            </template>
                        </el-table-column>
                        <el-table-column label="更新时间">
                            <template slot-scope="scope">
                                {{returnDate(scope.row.lastUpdateTime)}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="labelStatusName"
                                         label="标签状态">
                        </el-table-column>
                        <el-table-column label="操作"
                                         width="150">
                            <template slot-scope="scope">
                                <el-button @click="lookLabel(scope.row.labelId)"
                                           type="text"
                                           size="middle">查看</el-button>
                                <el-button @click="changeLabel(scope.row.labelId, 2)"
                                           v-if="scope.row.labelStatusName === '激活'"
                                           type="text"
                                           :disabled="scope.row.labelTypeName === '自动标签'"
                                           size="middle">停用</el-button>
                                <el-button @click="changeLabel(scope.row.labelId, 1)"
                                           v-else
                                           type="text"
                                           :disabled="scope.row.labelTypeName === '自动标签'"
                                           size="middle">激活</el-button>
                                <el-button @click="deleteLabel(scope.row.labelId)"
                                           type="text"
                                           :disabled="!(scope.row.labelType === 1 && scope.row.labelStatus === 2)"
                                           size="middle">删除</el-button>
                            </template>
                        </el-table-column>
                    </ft-table>
                </div>
                <div class="table-footer">
                    <div class="pick-box">
                        <span class="num">已选 {{pickedCount}} / {{total}}</span>
                        <el-dropdown @command="handleCommand">
                            <el-button type="primary"
                                       size="mini">
                                批量操作<i class="el-icon-arrow-down el-icon--right"></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="stop">停用所选</el-dropdown-item>
                                <el-dropdown-item command="delete">删除所选</el-dropdown-item>
                                <el-dropdown-item command="activate">激活所选</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                    <div class="page-box">
                        <el-pagination @size-change="pageSizeChange"
                                       @current-change="pageChange"
                                       :current-page="searchData.page"
                                       :page-sizes="[10, 20, 50]"
                                       :page-size="searchData.pageSize"
                                       layout="total, sizes, prev, pager, next, jumper"
                                       :total="total">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import {
    tagList
} from '@/utils/tagsSearch';
import timeHandle from '../../utils/timeHandle.js';
import FtTable from '../../components/ft-table';

export default {
    name: 'tagList',
    components: { FtTable },
    data() {
        return {
            total: 0, // 所有数据条数
            screen: true, // 筛选框显示
            dataList: [], // 数据列表
            searchList: tagList, // 筛选条件列表
            showSearchData: {
            }, // 筛选条件（用于展示）
            searchData: {
                page: 1,
                pageSize: 10
            }, // 筛选条件（真正用的）
            pickedCount: 0 // 已选择的数量
        };
    },
    methods: {
        showScreen() {
            this.screen = !this.screen;
        },
        search() {
            const maxTime = new Date(this.showSearchData.createTimeMax).getTime();
            const minTime = new Date(this.showSearchData.createTimeMin).getTime();
            if (maxTime && minTime && maxTime < minTime) return this.$message.error('创建开始时间不能大于结束时间');
            this.$refs.myTable.clearPickedItems();
            this.showSearchData.page = 1;
            this.searchData = { ...JSON.parse(JSON.stringify(this.showSearchData)), pageSize: this.searchData.pageSize };
            this.getList();
            return '';
        },
        getList() {
            this.$loading('加载中...');
            this.$get('/crm/label/list.json', this.searchData).then((res) => {
                const dataList = res.data.object.list;
                this.dataList = dataList;
                this.total = res.data.object.paging.total;
                // 若当前页没有数据，则跳到第一页
                if (dataList.length <= 0 && this.searchData.page !== 1) {
                    this.searchData.page = 1;
                    this.getList();
                }
            }).finally(() => {
                this.$loading().close();
            });
        },
        resetSearchData() {
            // 重置searchData
            this.searchData = { page: 1, pageSize: this.searchData.pageSize };
            // 给searchData添加属性
            // 必须用$set()，否则不触发视图更新
            this.searchList.forEach((item) => {
                if (item.type === 'select') {
                    this.$set(this.showSearchData, item.key, item.list.length ? item.list[0].value || '0' : '0');
                } else if (item.type === 'input') {
                    this.$set(this.showSearchData, item.key, '');
                } else {
                    this.$set(this.showSearchData, `${item.key}Min`, '');
                    this.$set(this.showSearchData, `${item.key}Max`, '');
                }
            });
        },
        clearSearch() {
            this.resetSearchData();
            this.search();
        },
        clear(i) {
            // 清除指定筛选项
            this.showSearchData[`${this.searchList[i].key}Min`] = '';
            this.showSearchData[`${this.searchList[i].key}Max`] = '';
        },
        addNew() {
            this.$router.push('/tag/detail');
        },
        lookLabel(labelId) {
            this.$router.push(`/tag/detail?id=${labelId || '123'}`);
        },
        handleCommand(e) {
            const labelIds = this.returnLabelIds();
            if (e === 'stop') {
                this.changeLabel(labelIds, 2);
            } else if (e === 'delete') {
                this.deleteLabel(labelIds);
            } else if (e === 'activate') {
                this.changeLabel(labelIds, 1);
            }
        },
        returnLabelIds() {
            let labelIds = '';
            const pickList = this.$refs.myTable.getPickedItems();
            pickList.forEach((item) => {
                labelIds += `${item.labelId},`;
            });
            labelIds = labelIds.substring(0, labelIds.length - 1);
            return labelIds;
        },
        changeLabel(labelIds, labelStatus) {
            if (!labelIds) return this.$message.error('请选择内容');
            labelIds = `${labelIds}`;
            const many = labelIds.indexOf(',');
            this.$confirm(`确定要${labelStatus === 1 ? '激活' : '停用'}这${many > 0 ? '些' : '条'}规则吗?`).then(() => {
                const params = {
                    labelStatus, // 规则状态  0:全部 1:激活 2:停用
                    labelIds,
                };
                this.$post('/crm/label/updateStatus.json', params).then(() => {
                    this.$message.success('操作成功');
                    // 清空已选择
                    this.$refs.myTable.clearPickedItems();
                    this.getList();
                });
            });
            return '';
        },
        deleteLabel(labelIds) {
            if (!labelIds) return this.$message.error('请选择内容');
            labelIds = `${labelIds}`;
            const many = labelIds.indexOf(',');
            this.$confirm(`确定要删除这${many > 0 ? '些' : '条'}规则吗?`).then(() => {
                this.$get('/crm/label/delete.json', { labelIds }).then(() => {
                    this.$message.success('操作成功');
                    // 清空已选择
                    this.$refs.myTable.clearPickedItems();
                    this.getList();
                });
            }).catch(error => console.log(error));
            return '';
        },
        selectChange(item) {
            if (item.name === '标签大类' && this.showSearchData.labelPartition === '1') this.showSearchData.labelGroupId = '0';
        },
        pageChange(e) {
            this.searchData.page = e;
            this.getList();
        },
        pageSizeChange(e) {
            this.searchData.pageSize = e;
            // this.showSearchData.page = 1;
            // this.searchData.page = 1;
            // this.searchData.page = 1;
            this.getList();
        },
        returnDate(time) {
            return timeHandle.returnDate(time, 'hour');
        },
        hasItem(row) {
            return pickItem => pickItem.labelId === row.labelId;
        },
        equalItem(item1, item2) {
            return item1.labelId === item2.labelId;
        },
        pickListChange(pickedList) {
            this.pickedCount = pickedList.length || 0;
        },
        getTagGroupList() {
            this.$get('/crm/label/group/list.json').then((res) => {
                this.searchList[1].list = res.data.object;
                // 获取的列表中，首位添加一个‘全部‘
                this.searchList[1].list.unshift({
                    labelGroupId: '0',
                    labelGroupName: '全部'
                });
            });
        }
    },
    mounted() {
        this.resetSearchData();
        // 获取标签分组列表
        this.getTagGroupList();
        this.search();
    }
};
</script>

