<template>
    <div class="container">
        <div class="header">{{title}} <el-button class="back-Btn" size="small" type="primary" icon="el-icon-back" @click="back">返回</el-button> </div>
        <el-form label-position="left" label-width="80px" :model="form">
            <el-form-item v-if="edit" required label="上传">
                <el-upload
                        action="/uploadImage.html?action=uploadImage"
                        :file-list="this.form.pictureList"
                        :limit="1"
                        :before-upload="beforeUpload"
                        :on-success="uploadSuccess"
                        :on-error="uploadError"
                        :disabled="Boolean(edit && this.form.pictureList[0])"
                        :show-file-list="false"
                        list-type="picture">
                    <el-button size="small" :disabled="Boolean(edit && this.form.pictureList[0])" type="primary">点击上传</el-button>
                    <div slot="tip" class="el-upload__tip">上传图片不能超过5M</div>
                </el-upload>
                <template v-if="this.form.pictureList[0]">
                    <div class="picture">
                        <div class="picture">
                            <img class="picture-max" :src="this.form.pictureList[0].url" alt="">
                        </div>
                        <div class="picture2">
                            <el-button class="picture-delete" type="danger" icon="el-icon-delete" circle @click="deletePicture"></el-button>
                        </div>
                    </div>
                </template>
            </el-form-item>
            <el-form-item v-else required label="图片">
                <template v-if="this.form.pictureList[0]">
                    <div class="picture">
                        <div class="picture">
                            <img class="picture-max" :src="this.form.pictureList[0].url" alt="">
                        </div>
                    </div>
                </template>
            </el-form-item>
            <titleInput :form="form" :edit="!edit"></titleInput>
            <el-form-item label="内容标签">
                <tagList v-if="edit" :labelList="this.form.tagList"></tagList>
                <ft-labels v-else :labels="this.form.tagList"></ft-labels>
            </el-form-item>
            <el-form-item v-if="edit">
                <el-button type="primary" @click="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import tagList from '../components/tagList';
import titleInput from '../components/titleInput';

export default {
    name: 'pictureEdit',
    components: {
        tagList,
        titleInput
    },
    data() {
        return {
            title: '上传图片',
            edit: true,
            id: 0,
            form: this.createEmptyData()
        };
    },
    created() {
        this.checkPageStatus();
    },
    methods: {
        checkPageStatus() {
            if (this.$route.query.id) {
                // action为edit，同时id不为空
                // 此时为 查看页面 不可编辑
                this.edit = this.$route.query.action === 'edit';
                this.title = this.edit ? '编辑图片' : '查看图片';
                this.id = this.$route.query.id;
                this.findDataById(this.id);
            } else {
                // 此时为 新增页面
                this.title = '上传图片';
                this.edit = true;
                this.id = 0;
                this.resetData();
            }
        },
        createEmptyData() {
            return {
                picture: '',
                pictureList: [],
                title: '',
                tagList: [],
            };
        },
        resetData() {
            this.form = this.createEmptyData();
        },
        findDataById(resourceId) {
            this.$loading({
                text: '获取数据中...'
            });
            return this.$get('/resources/findResourcesById.json', { resourceId }).then((res) => {
                this.$loading().close();
                if (res.data.retcode === 200) {
                    const data = res.data.object;
                    this.form.title = data.resource.title;
                    this.form.pictureList = [{ url: data.resource.imageUrl }];
                    data.labels.forEach((value) => {
                        value.closable = true;
                    });
                    this.form.tagList = data.labels;
                } else {
                    this.form.pictureList = [];
                    this.$alert('获取数据失败');
                }
                return Promise.resolve(res);
            });
        },
        beforeUpload(file) {
            if (this.form.pictureList.length === 1) {
                return false;
            }
            if (!/(.jpg|.png|.jpeg)$/.test(file.name)) {
                this.$alert('文件格式必须是jpg,png或jpeg');
                return false;
            }
            if (file.size > 1024 * 1024 * 5) {
                this.$alert(`图片大小必须在5M以内，上传图片大小为：${Number(file.size / (1024 * 1024)).toFixed(2)}M`);
                return false;
            }
            return true;
        },
        uploadSuccess(res) {
            if (res.retcode === 200 && res.imageUrl) {
                this.$message.success('上传成功');
                this.form.pictureList = [{ url: res.imageUrl }];
            } else {
                this.$alert('上传失败');
            }
        },
        uploadError() {
            this.$alert('上传图片失败');
        },
        deletePicture() {
            this.$confirm('是否删除图片？').then(() => {
                this.form.pictureList = [];
            });
        },
        submit() {
            const params = {
                resourceType: 3,
                title: this.form.title,
                labels: this.form.tagList.map(label => label.labelId).join(','),
                imageUrl: this.form.pictureList[0] && this.form.pictureList[0].url
            };
            if (this.id) {
                params.resourceId = this.id;
            }
            if (params.title.length < 1) {
                this.$alert('请输入图片标题');
                return;
            } if (params.title.length > 30) {
                this.$alert('图片标题的长度超过了30,请适当减少');
                return;
            }
            if (!params.imageUrl) {
                this.$alert('必须上传图片才能保存');
                return;
            }
            this.$loading({
                text: '保存数据中...'
            });
            this.$post('/resources/addOrModifyResource.json', params).then(() => {
                this.$loading().close();
                this.$alert('保存成功').then(() => {
                    this.back();
                });
            }).finally(() => {
                this.$loading().close();
            });
        },
        back() {
            this.$router.push('/marketing/list?type=picture');
        }
    }

};
</script>

<style scoped>
    .back-Btn{
        float: right;
    }
    .picture{
        display: flex;
        align-items: center;
        justify-content: start;
    }
    .picture2{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .picture-delete{
        height: 40px;
        margin-left: 20px;
    }
    .picture-max{
        max-height: 300px;
        max-width: 500px;
    }
</style>
