<template>
    <div>
        <label-picker class="tag-chosen-field"
                      :searchFunc="searchFunc"
                      :labels="labelList"
                      :auto-search="Boolean(true)"
                      :refresh-after-hide="Boolean(true)"
                      @pickLabel="pickLabel"
                      @deleteLabel="deleteLabel">
        </label-picker>
    </div>
</template>

<script>
export default {
    name: 'tagList',
    props: {
        labelList: {
            required: true,
            type: Array
        },
        labelType: {
            default() {
                return 1;
            }
        }
    },
    methods: {
        // 标签过滤方法
        searchFunc(key) {
            const params = {
                labelPartitions: '2,3', // 1表示用户标签
                labelType: Number(this.labelType), // 1,手动标签；2,自动标签 0是全部
                labelGroupId: 0 // 暂时传0
            };
            if (key) {
                params.labelName = key;
            }
            return this.$get('/crm/label/search.json', params).then((res) => {
                let { object } = res.data;
                if (object && object.length) {
                    object.forEach((item) => {
                        item.closable = true;
                    });
                    object = object.filter(label => label.labelPartition === 2 || label.labelPartition === 3);
                    return Promise.resolve(object);
                }

                return Promise.resolve([]);
            });
        },
        pickLabel(label = {}) {
            if (label.labelName && this.labelList.findIndex(item => item.labelId === label.labelId) < 0) {
                this.labelList.push(label);
            }
        },
        deleteLabel(label = {}) {
            const index = this.labelList.indexOf(label);
            if (index > -1) {
                this.labelList.splice(index, 1);
            }
        }
    }

};
</script>

