<template>
    <div class="home">
        <div class="header">
            调研问卷
        </div>
        <div class="content">
            <div class="item">
                <div class="item-title">
                    <span>筛选</span>
                    <div class="btn-box">
                        <el-button @click="search()"
                                   size="mini">查询结果</el-button>
                    </div>
                </div>
                <div class="item-content">
                    <div class="search-box">
                        <div class="clear-box">
                            <el-button @click="clearSearch()"
                                       size="mini">清除筛选</el-button>
                        </div>
                    </div>
                    <div class="item-list">
                        <span class="search-name">
                            创建人：
                        </span>
                        <el-select v-model="searchData.createMan"
                                   size="small">
                            <el-option v-for="(man, index) in createManList"
                                       :key="index"
                                       :label="man.userName"
                                       :value="man.userId">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="item-list">
                        <span class="search-name">
                            创建时间：
                        </span>
                        <el-date-picker v-model="searchData.timeMin"
                                        type="datetime"
                                        size="small"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        placeholder="选择开始日期时间">
                        </el-date-picker>
                        至
                        <el-date-picker v-model="searchData.timeMax"
                                        type="datetime"
                                        size="small"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        placeholder="选择结束日期时间">
                        </el-date-picker>
                        <span class="clear"
                              @click="clearTime()">清除</span>
                    </div>
                    <div class="item-list">
                        <el-input size="small"
                                  v-model="searchData.keyWord"
                                  placeholder="问卷标题"></el-input>
                    </div>
                </div>
            </div>
            <div class="addNew">
                <el-button @click="addNew()"
                           size="mini"
                           type="success">添加问卷</el-button>
            </div>
            <div class="item">
                <div class="item-title">
                    <span>数据列表</span>
                </div>
                <div class="item-content padding0">

                    <el-table ref="myTable"
                              border
                              :data="dataList">
                        <el-table-column prop="questionId"
                                         label="序号">
                        </el-table-column>
                        <el-table-column prop="questionId"
                                         label="问券标题">
                        </el-table-column>
                        <el-table-column prop="subjectCount"
                                         label="内容ID">
                        </el-table-column>
                        <el-table-column prop="subjectCount"
                                         label="问题数量">
                        </el-table-column>
                        <el-table-column label="问券二维码">
                            <template slot-scope="scope">
                                <img :src="scope.row.qrcode"
                                     class="qrCode"
                                     @click="lookQrCode(scope.row.qrcode)">
                            </template>
                        </el-table-column>
                        <el-table-column prop="questionUrl"
                                         label="问卷链接">
                        </el-table-column>
                        <el-table-column prop="finishCount"
                                         label="完成人数">
                        </el-table-column>
                        <el-table-column label="创建时间"
                                         width="165">
                            <template slot-scope="scope">
                                {{returnDate(scope.row.createTime)}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="createUser"
                                         label="创建人">
                        </el-table-column>
                        <el-table-column label="操作"
                                         width="150">
                            <template slot-scope="scope">
                                <el-button @click="lookQuestion(scope.row.questionId)"
                                           type="text"
                                           size="middle">查看问券</el-button>
                                <el-button @click="exportDetail(scope.row)"
                                           type="text"
                                           size="middle">导出明细</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>
            <el-pagination background
                           @current-change="pageChange"
                           :current-page="searchData.page"
                           :page-size="searchData.pageSize"
                           layout="prev, pager, next"
                           :total="1000">
            </el-pagination>
        </div>

        <preview :visible="qrCodeShow"
                 :srcType="'image'"
                 :src="qrCode"
                 @hidden="hidePreview"></preview>
    </div>
</template>

<script>

import timeHandle from '../../utils/timeHandle.js';

export default {
    name: 'questionList',
    data() {
        return {
            createManList: ['全部', '赵四'],
            qrCode: '',
            qrCodeShow: false,
            total: 1000, // 所有数据条数
            dataList: [], // 数据列表
            searchData: {
                page: 1,
                pageSize: 20,
                timeMin: '',
                timeMax: '',
                createMan: '0',
                keyWord: '',
            }, // 筛选条件（真正用的）
        };
    },
    methods: {
        getUsers() {
            this.$get('/question/getUsers.json').then((res) => {
                this.createManList = res.data.object.list;
                this.createManList.unshift({
                    userId: '0',
                    userName: '全部'
                });
            });
        },
        search() {
            const maxTime = new Date(this.searchData.timeMax).getTime();
            const minTime = new Date(this.searchData.timeMin).getTime();
            if (maxTime && minTime && maxTime < minTime) return this.$message.error('创建开始时间不能大于结束时间');
            this.searchData.page = 1;
            this.getList();
            return '';
        },
        getList() {
            this.$get('/question/getQuestions.json', this.searchData).then((res) => {
                this.dataList = res.data.object.list;
                this.total = res.data.object.paging.total;
            });
        },
        clearTime() {
            this.searchData.timeMin = '';
            this.searchData.timeMax = '';
        },
        clearSearch() {
            this.searchData.createMan = '全部';
            this.searchData.timeMin = '';
            this.searchData.timeMax = '';
            this.searchData.keyWord = '';
            this.search();
        },
        addNew() {
            this.$router.push('/question/detail');
        },
        lookQuestion(id) {
            this.$router.push(`/question/detail?id=${id || '123'}`);
        },
        exportDetail(id) {
            window.open(`/question/exportQuestions.json?questionId=${id}`);
        },
        pageChange(e) {
            this.searchData.page = e;
            this.getList();
        },
        returnDate(time) {
            return timeHandle.returnDate(time, 'hour');
        },
        lookQrCode(src) {
            this.qrCodeShow = true;
            this.qrCode = src;
        },
        hidePreview() {
            this.qrCodeShow = false;
        }
    },
    mounted() {
        this.getUsers();
        this.search();
    }
};
</script>

