<template>
    <div class="label-wrapper">
        <!--弹出框搜索标签-->
        <pick-list-popover ref="popover"
                           :data="searchLabels"
                           :refresh-after-hide="refreshAfterHide"
                           :auto-search="autoSearch"
                           :debounce="debounce"
                           :searchFunc="searchFunc"
                           :placeHolder="placeHolder"
                           :disabled="disabled"
                           @pick="_pickLabel">
            <template slot="list">
                <el-table-column>
                    <template slot-scope="scope">
                        <span class="search-list-item">
                            {{ scope.row.labelName }}
                        </span>
                    </template>
                </el-table-column>
            </template>
            <template slot="icon">
                <div class="icon-wrapper"
                     :class="disabled ? 'icon-not-allow' : ''"
                     slot="reference">
                    <i class="el-icon-circle-plus-outline icon-line-height-fix"></i>
                </div>
            </template>
        </pick-list-popover>
        <!--标签列表-->
        <ft-labels class="labels"
                   :labels="labels"
                   @deleteLabel="_deleteLabel"></ft-labels>
    </div>
</template>

<script>
export default {
    name: 'label-picker',
    props: {
        searchFunc: {
            type: Function,
            default: () => Promise.resolve([])
        },
        labels: {
            type: Array,
            default: () => []
        },
        searchLabels: {
            type: Array,
            default: () => []
        },
        autoSearch: {
            type: Boolean,
            default: false
        },
        refreshAfterHide: {
            type: Boolean,
            default: false
        },
        placeHolder: {
            type: String,
            default: '请输入内容'
        },
        debounce: {
            type: Number,
            default: 300
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            deletingLabel: {},
            dialogVisible: false
        };
    },
    methods: {
        _pickLabel(label) {
            this.$emit('pickLabel', label, this.labels);
        },
        _deleteLabel(index, labels) {
            if (index > -1) {
                this.$emit('deleteLabel', this.labels[index], labels);
            }
        },
        search(key) {
            this.$refs.popover.search(key);
        }
    }
};
</script>

<style scoped lang="scss">
.label-wrapper {
  display: flex;
  vertical-align: middle;

  .icon-wrapper {
    display: inline-block;
    font-size: 24px;
    vertical-align: middle;
    color: #409eff;
    line-height: 42px;
    height: 42px;

    &.icon-not-allow {
        cursor: not-allowed;
        color: #c0c4cc;
    }

    .icon-line-height-fix {
      line-height: 42px;
      height: 42px;
    }
  }

  .labels {
    flex: 1;
  }
}

.search-list-item {
  margin-left: 10px;
  font-size: 14px;
  line-height: 23px;
}
</style>
