<template>
    <div>
        <el-row class="content-nav-container">
            <el-col :span="6">
                <el-menu
                        default-active="1"
                        class="content-nav"
                        @select="selectMenu">
                    <el-menu-item index="1">
                        <i class="el-icon-menu"></i>
                        <span slot="title">链接</span>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span slot="title">视频</span>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-menu"></i>
                        <span slot="title">图片</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-menu"></i>
                        <span slot="title">问卷</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
            <el-col :span="18">
                <search-list
                    :resourceType="1"
                    :btnName="'新建图文'"
                    :class="currentListIndex === '1' ? 'show' : 'hide'"
                    @select="select"
                    @clear-selected="clearSelected"
                ></search-list>
                <search-list
                    :resourceType="2"
                    :btnName="'添加视频'"
                    :class="currentListIndex === '2' ? 'show' : 'hide'"
                    @select="select"
                    @clear-selected="clearSelected"
                ></search-list>
                <search-list
                    :resourceType="3"
                    :btnName="'上传图片'"
                    :class="currentListIndex === '3' ? 'show' : 'hide'"
                    @select="select"
                    @clear-selected="clearSelected"
                ></search-list>
                <search-list
                    :resourceType="4"
                    :class="currentListIndex === '4' ? 'show' : 'hide'"
                    @select="select"
                    @clear-selected="clearSelected"
                ></search-list>
            </el-col>
        </el-row>
        <el-row class="content-select-operation">
            <el-col :span="6" class="content-select-tip">
                <span>已选</span>{{selectedResource[currentListIndex].length}}<span>个</span>，<span>可选</span>1<span>个</span>
            </el-col>
            <el-col :span="10" :offset="8" class="content-select-btns">
                <el-button
                        style="width: 150px;"
                        type="danger"
                        :disabled="selectedResource[currentListIndex].length === 0"
                        @click="selectConfirm">确定</el-button>
                <el-button
                        style="width: 150px;"
                        @click="cancelSelect"
                >取消</el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import searchList from './searchList';

export default {
    name: 'contentLibrary',
    data() {
        return {
            currentListIndex: '1',
            selectedResource: {
                1: [],
                2: [],
                3: [],
                4: []
            }
        };
    },
    components: {
        searchList
    },
    methods: {
        selectMenu(index) {
            this.currentListIndex = index;
        },
        select(resource) {
            if (resource) {
                const index = this.selectedResource[this.currentListIndex].findIndex(
                    item => item.resourceId === resource.resourceId
                );
                if (index === -1) {
                    this.selectedResource[this.currentListIndex].splice(0, 1, resource);
                }
            }
        },
        clearSelected(resource) {
            if (resource) {
                const index = this.selectedResource[this.currentListIndex].findIndex(
                    item => item.resourceId === resource.resourceId
                );
                if (index > -1) {
                    this.selectedResource[this.currentListIndex].splice(index, 1);
                }
            }
        },
        selectConfirm() {
            this.$emit('select-confirm', this.selectedResource[this.currentListIndex]);
        },
        cancelSelect() {
            this.$emit('cancel-select');
        },
        clearSelectedResource() {
            this.selectedResource = {
                1: [], 2: [], 3: [], 4: []
            };
        }
    }
};
</script>

<style scoped lang="scss">
.content-nav-container {
    overflow: hidden;

    .content-nav {
        margin-bottom: -9999px;
        padding-bottom: 9999px;
    }
}

.content-select-operation {
    margin-top: 20px;

    .content-select-tip {
        line-height: 40px;
    }

    .content-select-btns {
        text-align: right;
    }
}

.show {
    display: block;
}
.hide {
    display: none;
}
</style>
