import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        menu: [],
        userInfo: {},
        operationInfo: []
    },
    mutations: {
        setMenu(state, menu) {
            // 变更状态
            state.menu = menu;
        },
        setUserInfo(state, userInfo) {
            // 变更状态
            state.userInfo = userInfo;
        },
        setOperation(state, operationInfo) {
            // 变更状态
            state.operationInfo = operationInfo;
        }
    }
});
