<template>
    <div class="container">
        <div class="header-wrapper">
            <div class="header">
                <el-row>
                    <el-col :span="2">
                        <span>群发管理</span>
                    </el-col>
                    <el-col :span="22">
                        <div style="float: right;">
                            <el-button type="danger"
                                       @click="newGroupMsg">新建群发</el-button>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <div class="options">
                <el-form :inline="true"
                         :model="searchParams"
                         class="options-form">
                    <el-form-item class="option-item">
                        <el-input style="width: 250px;"
                                  v-model="searchParams.taskTitle"
                                  placeholder="请按标题关键字搜索">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="发送时间："
                                  class="option-item">
                        <el-date-picker v-model="searchParams.taskSendTimeStart"
                                        type="datetime"
                                        placeholder="发送时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="至"
                                  class="option-item">
                        <el-date-picker v-model="searchParams.taskSendTimeEnd"
                                        type="datetime"
                                        placeholder="结束时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="内容分类："
                                  class="option-item">
                        <el-select v-model="searchParams.taskContentCategory"
                                   placeholder="请选择">
                            <el-option label="营销软文"
                                       value="0"></el-option>
                            <el-option label="调研问卷"
                                       value="1"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="内容类型："
                                  class="option-item">
                        <el-select v-model="searchParams.resourceType"
                                   placeholder="请选择"
                                   :disabled="searchParams.taskContentCategory === '1'">
                            <el-option label="全部"
                                       value="-1"></el-option>
                            <el-option label="链接"
                                       value="1"></el-option>
                            <el-option label="视频"
                                       value="2"></el-option>
                            <el-option label="图文"
                                       value="3"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <el-form :inline="true"
                         :model="searchParams"
                         class="options-form">
                    <el-form-item label="渠道分类："
                                  class="option-item">
                        <el-select v-model="searchParams.channelCategory"
                                   placeholder="请选择">
                            <el-option label="个人号"
                                       value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="渠道类型："
                                  class="option-item">
                        <el-select v-model="searchParams.nickName"
                                   placeholder="请选择">
                            <el-option v-for="(condition, index) in searchConditions"
                                       :key="index"
                                       :label="condition.nickName"
                                       :value="condition.nickName">
                                {{condition.nickName}}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="发送状态："
                                  class="option-item">
                        <el-select v-model="searchParams.taskSendStatus"
                                   placeholder="请选择">
                            <el-option label="全部"
                                       value="-1"></el-option>
                            <el-option label="未发送"
                                       value="0"></el-option>
                            <el-option label="发送中"
                                       value="1"></el-option>
                            <el-option label="已发送"
                                       value="2"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <el-form :inline="true">
                    <el-form-item>
                        <div class="label-container">
                            <span class="label-title">用户标签：</span>
                            <label-picker :searchFunc="searchFunc"
                                          :labels="labels"
                                          :auto-search="Boolean(true)"
                                          :refresh-after-hide="Boolean(true)"
                                          @pickLabel="pickLabel"
                                          @deleteLabel="deleteLabel">
                            </label-picker>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary"
                                   @click="search">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="list">
            <el-table :data="tableData"
                      border
                      style="width: 100%">

                <el-table-column label="序号"
                                 type="index">
                </el-table-column>

                <el-table-column :label="contentIDName"
                                 prop="resources.resourceId">
                </el-table-column>

                <el-table-column label="标题"
                                 prop="resources.title">
                </el-table-column>

                <el-table-column label="内容"
                                 width="180">
                    <template slot-scope="scope">
                        <template v-if="scope.row.resources.resourceType === 1 || scope.row.resources.resourceType === 4">
                            <a href="javascript:void(0)" @click="showPreview(scope.row.resources)">
                                {{shortcutLinkUrl(scope.row.resources.linkUrl)}}
                            </a>
                        </template>
                        <template v-if="scope.row.resources.resourceType === 2">
                            <div class="video-wrapper" @click="showPreview(scope.row.resources)">
                                <video class="table-video"
                                       :src="scope.row.resources.vedioUrl"
                                       controls></video>
                                <div class="video-mask">
                                    <span class="video-duration">{{videoLength(scope.row.resources.vedioLength)}}</span>
                                </div>
                            </div>
                        </template>
                        <template v-if="scope.row.resources.resourceType === 3">
                            <img class="table-img"
                                 :src="scope.row.resources.imageUrl"
                                 @click="showPreview(scope.row.resources)" />
                        </template>
                    </template>
                </el-table-column>

                <el-table-column label="内容类型"
                                 width="100px;">
                    <template slot-scope="scope">
                        {{resourceType(scope.row.resources.resourceType)}}
                    </template>
                </el-table-column>

                <el-table-column label="发送时间">
                    <template slot-scope="scope">
                        {{timeFormat(scope.row.taskSendTime)}}
                    </template>
                </el-table-column>

                <el-table-column label="发送成功"
                                 prop="taskSendSuccess">
                    <template slot-scope="scope">
                        {{scope.row.taskSendSuccess || 0}}人
                    </template>
                </el-table-column>

                <el-table-column label="发送失败"
                                 prop="taskSendSuccess">
                    <template slot-scope="scope">
                        {{scope.row.taskSendFail || 0}}人
                    </template>
                </el-table-column>

                <el-table-column label="发送状态">
                    <template slot-scope="scope">
                        {{taskSendStatus(scope.row.taskSendStatus)}}
                    </template>
                </el-table-column>

                <el-table-column label="操作"
                                 width="200">
                    <template slot-scope="scope">
                        <el-button size="mini"
                                   @click="handleView(scope.$index, scope.row)">查看</el-button>
                        <el-button v-if="scope.row.taskSendStatus !== 0"
                                   size="mini"
                                   @click="handleExport(scope.$index, scope.row)">导出</el-button>
                        <el-button v-if="scope.row.taskSendStatus === 0"
                                   size="mini"
                                   type="danger"
                                   @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>

            </el-table>
            <el-row class="footer-pagination">
                <el-pagination @current-change="pageChange"
                               @size-change="sizeChange"
                               :current-page="paging.page"
                               :page-sizes="[10, 30, 50]"
                               :page-size="paging.pageSize"
                               layout="total, sizes, prev, pager, next, jumper"
                               :total="paging.total">
                </el-pagination>
            </el-row>
        </div>

        <preview :visible="previewVisible"
                 :srcType="previewData.type"
                 :src="previewUrl(previewData.url, previewData.type)"
                 @hidden="hidePreview"></preview>

    </div>
</template>

<script>
import dateUtil from '../../utils/date';
import { utils } from './mixin/utils';

const ramda = require('ramda');

const LABEL_SEARCH = '/crm/label/search.json';
const FIND_TASK_LIST = '/task/findTaskList.json';
const FIND_TASK_CONDITION = '/task/findTaskCondition.json';
const DELETE_TASK_BY_ID = '/task/deleteTaskById.json';
const EXPORT_TASK_BY_ID = '/task/exportTaskById.json';

export default {
    name: 'groupMsgList',
    mixins: [utils],
    data() {
        return {
            searchParams: {
                taskTitle: '', // 搜索关键字
                taskSendTimeStart: '', // 任务开始时间
                taskSendTimeEnd: '', // 任务结束时间
                taskContentCategory: '0', // 内容分类，默认为营销软文
                resourceType: '-1', // 内容类型
                channelCategory: '0', // 渠道分类，默认为个人号
                nickName: '', // 渠道类型
                taskSendStatus: '-1', // 发送状态
                labels: '' // 标签列表
            },
            searchConditions: [], // 渠道类型
            labels: [], // 标签列表
            tableData: [],
            paging: {
                page: 1, // 当前页码
                pageSize: 10, // 每页显示的条目数
                total: 0 // 总条目数
            },
            selectedResource: {},
            previewVisible: false,
            contentIDName: '内容ID'
        };
    },
    methods: {
        searchFunc(key) {
            const params = {
                labelPartitions: '1,3', // 1表示用户标签
                labelGroupId: 0 // 暂时传0
            };
            if (key) {
                params.labelName = key;
            }
            return this.$get(LABEL_SEARCH, params).then((res) => {
                const { object } = res.data;
                if (object && object.length) {
                    object.forEach((item) => {
                        item.closable = true;
                    });

                    return Promise.resolve(object);
                }

                return Promise.resolve([]);
            });
        },
        pickLabel(label) {
            if (label) {
                if (this.findLabelIndex(label) === -1) {
                    this.labels.push(label);
                }
            }
        },
        deleteLabel(label) {
            if (label) {
                const index = this.findLabelIndex(label);
                if (index > -1) {
                    this.labels.splice(index, 1);
                }
            }
        },
        findLabelIndex(label) {
            return this.labels.findIndex(item => item.labelId === label.labelId);
        },
        handlePage(params) {
            if (params) {
                params.page = this.paging.page;
                params.pageSize = this.paging.pageSize;
            }
            return params;
        },
        handleLabels(params) {
            const temp = [];
            this.labels.forEach(label => temp.push(label.labelId));
            params.labels = temp.join(',');
            return params;
        },
        handleSendTime(params) {
            if (this.searchParams.taskSendTimeStart) {
                params.taskSendTimeStart = dateUtil.formatDate(
                    this.searchParams.taskSendTimeStart, 'yyyy-MM-dd hh:mm:ss'
                );

                if (this.searchParams.taskSendTimeEnd) {
                    params.taskSendTimeEnd = dateUtil.formatDate(
                        this.searchParams.taskSendTimeEnd, 'yyyy-MM-dd hh:mm:ss'
                    );
                }
            } else if (this.searchParams.taskSendTimeEnd) {
                this.$alert('请选择发送时间！');
                return false;
            }

            if (this.searchParams.taskSendTimeEnd && this.searchParams.taskSendTimeEnd) {
                if (this.searchParams.taskSendTimeEnd <= this.searchParams.taskSendTimeStart) {
                    this.$alert('发送结束时间应晚于发送开始时间！');
                    return false;
                }
            }

            return params;
        },
        handleParams() {
            const params = ramda.clone(this.searchParams);
            this.handlePage(params);
            this.handleLabels(params);
            if (!this.handleSendTime(params)) return false;
            return params;
        },
        findTaskList() {
            if (this.findTaskList.busy) return;
            this.findTaskList.busy = true;

            const data = this.handleParams();
            if (!data) {
                this.findTaskList.busy = false;
                return;
            }

            this.$loading();
            this.$get(FIND_TASK_LIST, data).then((res) => {
                this.tableData = res.data.object.list;
                this.paging = res.data.object.paging;

                this.switchContentIDName();
            }).finally(() => {
                this.findTaskList.busy = false;
                this.$loading().close();
            });
        },
        findTaskCondition() {
            return this.$get(FIND_TASK_CONDITION).then((res) => {
                this.searchConditions = res.data.object;
            });
        },
        resourceType(type) {
            switch (type) {
                case 1:
                    return '链接';
                case 2:
                    return '视频';
                case 3:
                    return '图文';
                default:
                    return '';
            }
        },
        taskSendStatus(type) {
            switch (type) {
                case 0:
                    return '未发送';
                case 1:
                    return '发送中';
                case 2:
                    return '已发送';
                default:
                    return '';
            }
        },
        pageChange(currentPage) {
            this.paging.page = currentPage;
            this.findTaskList();
        },
        sizeChange(pageSize) {
            this.paging.pageSize = pageSize;
            this.findTaskList();
        },
        resetChange() {
            this.paging.page = 1;
        },
        newGroupMsg() {
            this.$router.push('/groupMsg/edit');
        },
        search() {
            this.resetChange();
            this.findTaskList();
        },
        handleView(index, task) {
            if (task && task.taskId) this.$router.push(`/groupMsg/detail/${task.taskId}`);
        },
        handleExport(index, task) {
            window.open(`${EXPORT_TASK_BY_ID}?taskId=${task.taskId}`);
        },
        handleDelete(index, task) {
            if (this.handleDelete.busy) return;
            this.handleDelete.busy = true;

            this.$confirm(
                `确定删除此${this.resourceType(task.resources.resourceType)}?`,
                '提示',
                {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    type: 'warning'
                }
            ).then(() => this.$post(DELETE_TASK_BY_ID, { taskId: task.taskId })).then(() => {
                this.findTaskList();
            }).finally(() => {
                this.handleDelete.busy = false;
            });
        },
        showPreview(resource) {
            this.selectedResource = resource;
            this.previewVisible = true;
        },
        hidePreview() {
            this.previewVisible = false;
        },
        switchContentIDName() {
            if (this.searchParams.taskContentCategory === '0') {
                this.contentIDName = '内容ID';
                return;
            }

            this.contentIDName = '问卷ID';
        },
        timeFormat(timestamp) {
            if (timestamp) {
                return dateUtil.formatDate(new Date(timestamp), 'yyyy-MM-dd hh:mm:ss');
            }

            return '';
        }
    },
    computed: {
        previewData() {
            const { selectedResource } = this;
            const result = {};
            let url = '';
            let type = '';
            switch (selectedResource.resourceType) {
                case 1:
                case 4:
                    url = selectedResource.linkUrl;
                    type = 'link';
                    break;
                case 2:
                    url = selectedResource.vedioUrl;
                    type = 'video';
                    break;
                case 3:
                    url = selectedResource.imageUrl;
                    type = 'image';
                    break;
                default:
                    break;
            }

            result.url = url;
            result.type = type;

            return result;
        }
    },
    created() {
        this.findTaskCondition();
        this.findTaskList();
    }
};
</script>

<style scoped lang="scss">
.header-wrapper {
    .options {
        .options-form {
            .option-item {
                margin-right: 20px;

                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    .label-container {
        display: flex;
        max-width: 1245px;

        .label-title {
            height: 42px;
            line-height: 42px;
            min-width: 75px;
        }
    }
}

.label {
    height: 40px;
    line-height: 40px;
    text-align: center;
}

.table-img {
    width: 100%;
}

.video-wrapper {
    position: relative;
    font-size: 0;

    .table-video {
        width: 100%;
        background-color: black;
    }

    .video-mask {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.6);

        .video-duration {
            position: absolute;
            right: 5px;
            top: 5px;
            font-size: 14px;
            color: white;
            line-height: 1.4;
        }
    }
}

.footer-pagination {
    margin-top: 20px;
    text-align: right;
}
</style>
