import Vue from 'vue';
import Router from 'vue-router';
import axios from 'axios';
import Entry from './views/Entry.vue';
import Login from './views/Login.vue';
import NotFound from './views/NotFound.vue';
import AddRelation from './views/tag/AddRelation.vue';
import TagDetail from './views/tag/TagDetail.vue';
import TagList from './views/tag/TagList.vue';
import CrmUserList from './views/crm/CrmUserList.vue';
import UserInfo from './views/crm/UserInfo.vue';
import adList from './views/marketing/list.vue';
import adEdit from './views/marketing/edit/ad.vue';
import pictureEdit from './views/marketing/edit/picture.vue';
import videoEdit from './views/marketing/edit/video.vue';
import Home from './views/Home.vue';
import groupMsgList from './views/groupMsg/groupMsgList';
import groupMsgEdit from './views/groupMsg/groupMsgEdit';
import groupMsgDetail from './views/groupMsg/groupMsgDetail';
import QuestionList from './views/question/QuestionList';
import QuestionDetail from './views/question/QuestionDetail';

Vue.use(Router);

const scrollBehavior = () => ({ x: 0, y: 0 });

const router = new Router({
    mode: 'history',
    scrollBehavior,
    routes: [
        {
            path: '/',
            name: 'entry',
            component: Entry,
            children: [
                {
                    path: 'index',
                    name: 'index',
                    component: Home,
                    props: true
                },
                {
                    path: 'addRelation',
                    name: 'addRelation',
                    component: AddRelation,
                    props: true
                },
                {
                    path: 'tag/detail',
                    name: 'tagDetail',
                    component: TagDetail,
                    props: true
                },
                {
                    path: 'tag/list',
                    name: 'tagList',
                    component: TagList
                },
                {
                    path: '/crm/userList',
                    name: 'crmUserList',
                    component: CrmUserList,
                    props: true
                },
                {
                    path: 'userInfo',
                    name: 'userInfo',
                    component: UserInfo,
                    props: true
                },
                {
                    path: 'marketing/list',
                    name: 'adList',
                    component: adList,
                    props: true
                },
                {
                    path: 'marketing/ad/edit',
                    name: 'adEdit',
                    component: adEdit,
                    props: true
                },
                {
                    path: 'marketing/picture/edit',
                    name: 'pictureEdit',
                    component: pictureEdit,
                    props: true
                },
                {
                    path: 'marketing/video/edit',
                    name: 'videoEdit',
                    component: videoEdit,
                    props: true
                },
                {
                    path: 'groupMsg/list',
                    name: 'groupMsgList',
                    component: groupMsgList
                },
                {
                    path: 'groupMsg/edit',
                    name: 'groupMsgEdit',
                    component: groupMsgEdit
                },
                {
                    path: 'groupMsg/detail/:taskId',
                    name: 'groupMsgDetail',
                    component: groupMsgDetail
                },
                {
                    path: 'question/list',
                    name: 'questionList',
                    component: QuestionList
                },
                {
                    path: 'question/detail',
                    name: 'questionDetail',
                    component: QuestionDetail,
                    props: true
                }
            ]
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '*',
            name: 'notFound',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: NotFound
        }
    ]
});

function checkLogin() {
    return axios
        .request({
            url: '/checkLogin.json',
            method: 'get'
        })
        .then(
            (res) => {
                if (res.data.retcode === 200) {
                    return Promise.resolve('yes');
                }
                return Promise.resolve('no');
            },
            () => Promise.resolve('no')
        )
        .catch(() => Promise.resolve('no'));
}

router.beforeEach((to, from, next) => {
    const { fullPath } = to;
    console.log(fullPath);
    if (fullPath === '/') {
        next('/index');
        return;
    }
    checkLogin().then((result) => {
        if (fullPath === '/login') {
            if (result === 'no') {
                next();
            } else {
                next('/index');
            }
        } else if (result === 'yes') {
            next();
        } else {
            next('/login');
        }
    });
});

export default router;
