<template>
    <div class="home">
        <div class="header">
            {{type === 'add' ? '新建标签' : '标签详情'}}
            <div class="back-btn">
                <el-button v-show="isEditAndDeleteBtnShow"
                           @click="deleteTag()"
                           size="mini"
                           type="danger">删除标签</el-button>
                <el-button v-show="isEditAndDeleteBtnShow"
                           @click="editBtnClick()"
                           type="warning"
                           size="mini">编辑</el-button>
                <el-button v-show="isEdit && type === 'look' && params.labelType === '1'"
                           @click="save()"
                           size="mini">保存</el-button>
                <el-button v-show="type === 'add'"
                           @click="save()"
                           size="mini"
                           type="success">提交</el-button>
                <el-button @click="back()"
                           size="mini">返回</el-button>
            </div>
        </div>
        <div class="content">
            <div class="item">
                <div class="item-title">
                    <span>标签基础信息</span>
                </div>
                <div class="item-content">
                    <div class="list"
                         v-for="(item, index1) in showList"
                         :key="index1">
                        <span class="list-name">{{item.name}}：</span>
                        <el-select v-model="params[item.key]"
                                   :disabled="isDisabled(item)"
                                   size="small"
                                   @change="selectChange(item)">
                            <el-option v-for="(itemSelect, index2) in item.list"
                                       :key="index2"
                                       :label="itemSelect.name || itemSelect.labelGroupName"
                                       :value="itemSelect.value || itemSelect.labelGroupId">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="list">
                        <span class="list-name">标签名称：</span>
                        <div class="input-box">
                            <el-input v-model="params.labelName"
                                      :disabled="isShow2"
                                      maxlength="30"
                                      size="small"></el-input>
                            <p class="explain"
                               v-if="type === 'add'">标签名称最长不超过30字符，用于用户端展示的标签，推荐不超过5个字符</p>
                        </div>
                    </div>
                    <div class="list">
                        <span class="list-name">备注：</span>
                        <div class="input-box">
                            <el-input type="textarea"
                                      :autosize="{ minRows: 2, maxRows: 4}"
                                      placeholder="请输入内容"
                                      :disabled="isShow2"
                                      resize="none"
                                      maxlength="100"
                                      v-model="params.remark">
                            </el-input>
                            <p class="explain"
                               v-if="type === 'add'">请为标签添加必要的备注，对标签的规则或范围做完整地概括，最长不超过100字符</p>
                        </div>
                    </div>
                    <div class="line"></div>
                    <div class="list"
                         v-if="type === 'look'">
                        <span class="list-name">创建时间：</span>
                        {{createTime || ''}}
                    </div>
                    <div class="list"
                         v-if="type === 'look'">
                        <span class="list-name">更新时间：</span>
                        {{lastUpdateTime || ''}}
                    </div>
                    <div class="list">
                        <span class="list-name">标签状态：</span>
                        {{labelStatus || ''}}
                    </div>
                </div>
            </div>
            <div class="item"
                 v-if="type === 'look'">
                <div class="item-title">
                    <span>关联关系</span>
                    <span class="font-blue">已关联 {{categoryNum}} 个类目，共 {{totalNum}} 个关联</span>
                    <div class="back-btn"
                         v-if="params.labelType === '1' && type === 'look' && params.labelStatus === '1'">
                        <el-button @click="addRelation()"
                                   size="mini">增加关联关系</el-button>
                    </div>
                </div>
                <div class="item-content">
                    <div class="list">
                        <span class="list-name">用户关联数量：</span>
                        {{userRelationNum}}
                    </div>
                    <div class="list">
                        <span class="list-name">食材关联数量：</span>
                        {{ingredientsRelationNum}}
                    </div>
                    <div class="list">
                        <span class="list-name">营销软文关联数量：</span>
                        {{marketingRelationNum}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import {
    tagList
} from '@/utils/tagsSearch';
import timeHandle from '../../utils/timeHandle.js';

// 防止返回tagList页面时，import tagList会使用缓存数据（splice(5, 2)影响返回页面tagList数据）
const showList = JSON.parse(JSON.stringify(tagList));

export default {
    name: 'tagDetail',
    props: ['id'],
    data() {
        return {
            showList, // 展示列表
            type: 'add', // 页面类型，新建/查看
            isEdit: false, // 编辑状态
            params: {
                labelStatus: '',
                remark: '',
                labelName: '',
                labelPartition: '',
                labelGroupId: '',
                labelType: '',
                isUserShow: ''
            }, // 所有参数
            createTime: '', // 创建时间
            lastUpdateTime: '', // 更新时间
            userRelationNum: 0, // 用户关联数量
            ingredientsRelationNum: 0, // 食材关联数量
            marketingRelationNum: 0, // 营销软文关联数量
            categoryNum: 0, // 已关联类目数
            totalNum: 0 // 所有关联数
        };
    },
    computed: {
        isEditAndDeleteBtnShow() {
            return !this.isEdit && this.type === 'look' && this.params.labelType === '1' && this.params.labelStatus === '2';
        },
        isShow2() {
            return (!this.isEdit && this.type === 'look') || this.params.labelType === '2';
        },
        labelStatus() {
            const labelStatusMap = {
                1: '激活',
                2: '已停用'
            };
            return labelStatusMap[this.params.labelStatus] || '';
        }
    },
    created() {
        this.showList.splice(4, 3);
        this.showList.forEach((item) => {
            if (item.list.length) item.list[0].name = '请选择';
            this.$set(this.params, item.key, '0');
        });
        this.getTagGroupList();
        this.params.labelId = this.$route.query.id || '';
        this.type = this.params.labelId ? 'look' : 'add';
        // 新建标签默认选中手动标签
        if (!this.params.labelId) {
            this.params.labelType = '1';
            this.params.labelStatus = '1';
        }
        if (this.params.labelId) this.getDetail();
    },
    methods: {
        returnDate(time) {
            return timeHandle.returnDate(time, 'hour');
        },
        isDisabled(item) {
            return (item.name === '标签类型') // 标签类型禁用
            || (this.type === 'look' && !this.isEdit) // 查看详情时禁用
            || ((this.type === 'look' && this.isEdit) && (item.name === '标签大类' || item.name === '标签分组')) // 查看详情编辑状态下，禁用标签大类和标签分组
            || this.params.labelType === '2' // 标签类型为自动标签时，全部禁用
            || (this.params.labelPartition === '1' && item.name === '标签分组'); // 标签大类为用户标签时，标签分组禁用
        },
        selectChange(item) {
            if (item.name === '标签大类' && this.params.labelPartition === '1') this.params.labelGroupId = '0';
        },
        back() {
            if (this.isEdit) {
                this.isEdit = false;
                this.getDetail();
            } else {
                this.$router.push('/tag/list');
            }
        },
        getDetail() {
            this.$loading('加载中...');
            this.$get('/crm/label/detail.json', { labelId: this.params.labelId }).then((res) => {
                const data = res.data.object;
                // 以下选择表示，将后端传来的数字型转为字符串型
                // 为了避免null转为'null'，进行了是否为空的判断
                // 以下类型转换主要因为类型定义的不匹配，selection中的value会转为字符串型
                const fields = ['labelPartition', 'labelGroupId', 'isUserShow', 'labelStatus', 'labelType'];
                fields.forEach((item) => {
                    if (data[item] !== null) {
                        data[item] = `${data[item]}`;
                    }
                    this.params[item] = data[item];
                });
                // this.params.labelPartition = data.labelPartition && `${data.labelPartition}`;
                // this.params.labelGroupId = data.labelGroupId && `${data.labelGroupId}`;
                // this.params.isUserShow = data.isUserShow && `${data.isUserShow}`;
                // this.params.labelStatus = data.labelStatus && `${data.labelStatus}`;
                // this.params.labelType = data.labelType && `${data.labelType}`;
                this.params.labelName = data.labelName;
                this.createTime = this.returnDate(data.createTime);
                this.lastUpdateTime = this.returnDate(data.lastUpdateTime);
                this.params.remark = data.remark;
                this.userRelationNum = data.userRelationNum;
                this.ingredientsRelationNum = data.ingredientsRelationNum;
                this.marketingRelationNum = data.marketingRelationNum;
                this.categoryNum = this.categoryNum + (this.userRelationNum ? 1 : 0) + (this.ingredientsRelationNum ? 1 : 0) + (this.marketingRelationNum ? 1 : 0);
                this.totalNum = this.userRelationNum + this.ingredientsRelationNum + this.marketingRelationNum;
            }).finally(() => {
                this.$loading().close();
            });
        },
        deleteTag() {
            this.$confirm('确定则立即执行删除，不可撤销。标签的所有类目的关联关系均会同步删除。', '确认要删除当前标签吗？', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$loading('加载中...');
                this.$get('/crm/label/delete.json', { labelIds: this.params.labelId }).then(() => {
                    this.$message.success('操作成功');
                    this.$router.push('/tag/list');
                }).cath(error => console.log(error)).finally(() => {
                    this.$loading().close();
                });
            }).catch(error => console.log(error));
        },
        save() {
            const msg = this.checkData();
            if (msg) return this.$message.error(msg);
            this.$loading('加载中...');
            this.$post('/crm/label/addLabel.json', this.params).then(((res) => {
                this.$message.success('提交成功');
                if (res && res.data && res.data.object) {
                    const { labelId } = res.data.object;
                    this.$router.replace(`/tag/detail?id=${labelId}`);
                    this.type = 'look';
                    this.params.labelId = labelId;
                    this.isEdit = false;
                    this.getDetail();
                }
            })).catch(error => console.log(error)).finally(() => {
                this.$loading().close();
            });
            return '';
        },
        checkData() {
            let msg = '';
            if (!this.params.remark) msg = '备注信息有误';
            if (!this.params.labelName) msg = '标签名称信息有误';
            if (this.params.isUserShow === '0') msg = '标签展示类型信息有误';
            if (this.params.labelGroupId === '0' && this.params.labelPartition !== '1') msg = '标签分组信息有误';
            if (this.params.labelPartition === '0') msg = '标签大类信息有误';
            return msg;
        },
        addRelation() {
            this.$router.push(`/addRelation?id=${this.params.labelId}&type=${this.params.labelPartition}`);
        },
        getTagGroupList() {
            this.$get('/crm/label/group/list.json').then((res) => {
                let array = res.data.object;
                if (array instanceof Array) {
                    array = array.map(item => ({ ...item, labelGroupId: `${item.labelGroupId}` }));
                }
                this.showList[1].list = array;
                // 获取的列表中，首位添加一个‘全部‘
                this.showList[1].list.unshift({
                    labelGroupId: '0',
                    labelGroupName: '请选择'
                });
            }).catch(err => console.log(err));
        },
        editBtnClick() {
            this.isEdit = true;
            // 手动标签、激活状态、关联数量大于0，则拦截编辑
            // if (this.params.labelType === '1' && this.params.labelStatus === '1' && this.totalNum > 0) {
            //     this.$message.error('操作失败，当前数据不可编辑');
            // } else {
            //     this.isEdit = true;
            // }
        }
    }
};
</script>

