        margin-top: 20px;
        .item {
            margin-bottom: 10px;
            position: relative;
            &:last-of-type {
                margin-bottom: 100px;
                border-top: solid #dcdfe6 1px;
            }
            .item-title {
                position: relative;
                background: #dcdfe6;
                line-height: 50px;
                padding: 0 10px;
                font-size: 16px;
                .font-blue {
                    font-size: 13px;
                    color: #409eff;
                    margin-left: 20px;
                }
            }
            .btn-box {
                position: absolute;
                top: 20px;
