<template>
    <div class="container">
        <div class="header">{{title}} <el-button class="back-Btn" size="small" type="primary" icon="el-icon-back" @click="back">返回</el-button> </div>
        <el-form label-position="left" label-width="80px" :model="form">
            <titleInput :form="form" :edit="!edit"></titleInput>
            <el-form-item required label="详情">
                <div ref="editor" style="text-align:left"></div>
            </el-form-item>
            <el-form-item required label="封面">
                <el-upload
                        class="avatar-uploader"
                        action="/uploadImage.html?action=uploadImage"
                        :show-file-list="false"
                        :disabled="!edit"
                        :before-upload="beforeUpload"
                        :on-success="uploadSuccess"
                        :on-error="uploadError">
                    <div slot="tip">
                        <div class="error-text">图片比例为1:1.</div>
                        <div>建议在上传图片之前，对大于300kb的图片进行压缩。压缩图片网址：
                            <a href="https://tinypng.com/" target="_blank" class="blue-text">https://tinypng.com/</a></div>
                    </div>
                    <img v-if="this.form.picture" :src="this.form.picture" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="内容标签">
                <tagList v-if="edit" :labelList="this.form.tagList"></tagList>
                <ft-labels v-else :labels="this.form.tagList"></ft-labels>
            </el-form-item>
            <el-form-item v-if="edit">
                <el-button type="primary" @click="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import WangEditor from 'wangeditor';
import tagList from '../components/tagList';
import titleInput from '../components/titleInput';

export default {
    name: 'pictureEdit',
    components: {
        tagList,
        titleInput
    },
    data() {
        return {
            title: '新建链接',
            edit: true,
            id: 0,
            form: this.createEmptyData()
        };
    },
    mounted() {
        const editor = new WangEditor(this.$refs.editor);
        this.editor = editor;
        editor.customConfig.onchange = (html) => {
            this.form.linkContent = html;
        };
        // 显示 图片 - 上传图片
        editor.customConfig.customUploadImg = (files, insert) => {
            const formData = new FormData();
            formData.append('file', files[0]);
            this.$loading('上传图片中。。。');
            this.$post('/uploadImage.html?action=uploadImage', formData).then((res) => {
                this.$loading().close();
                // 上传代码返回结果之后，将图片插入到编辑器中
                insert(res.data.imageUrl);
            });
        };
        editor.customConfig.zIndex = 100;
        editor.create();
        this.checkPageStatus();
        editor.$textElem.attr('contenteditable', this.edit);
    },
    methods: {
        checkPageStatus() {
            if (this.$route.query.id) {
                // action为edit，同时id不为空
                // 此时为 查看页面 不可编辑
                this.edit = this.$route.query.action === 'edit';
                this.title = this.edit ? '编辑链接' : '查看链接';
                this.id = this.$route.query.id;
                this.findDataById(this.id);
            } else {
                // 此时为 新增页面
                this.title = '新建链接';
                this.edit = true;
                this.id = 0;
                this.resetData();
            }
        },
        createEmptyData() {
            return {
                picture: '',
                linkContent: '',
                title: '',
                tagList: [],
            };
        },
        resetData() {
            this.form = this.createEmptyData();
        },
        findDataById(resourceId) {
            this.$loading({
                text: '获取数据中...'
            });
            return this.$get('/resources/findResourcesById.json', { resourceId }).then((res) => {
                this.$loading().close();
                if (res.data.retcode === 200) {
                    const data = res.data.object;
                    this.form.title = data.resource.title;

                    this.form.linkContent = data.resource.linkContent;
                    if (this.editor) {
                        this.editor.txt.html(data.resource.linkContent);
                    }

                    this.form.picture = data.resource.imageUrl;
                    data.labels.forEach((value) => {
                        value.closable = true;
                    });
                    this.form.tagList = data.labels;
                } else {
                    this.form.picture = '';
                    this.$alert('获取数据失败');
                }
                return Promise.resolve(res);
            });
        },
        beforeUpload(file) {
            if (!/(.jpg|.png|.jpeg)$/.test(file.name)) {
                this.$alert('文件格式必须是jpg,png或jpeg');
                return false;
            }
            if (file.size > 300 * 1024) {
                this.$alert(`图片大小必须在300kb以内，上传图片大小为：${Number(file.size / 1024).toFixed(2)}kb`);
                return false;
            }
            return true;
        },
        uploadSuccess(res) {
            if (res.retcode === 200 && res.imageUrl) {
                this.$message.success('上传成功');
                this.form.picture = res.imageUrl || '';
            } else {
                this.$alert('上传失败');
            }
        },
        uploadError() {
            this.$alert('上传封面图片失败');
        },
        deletePicture() {
            this.$confirm('是否删除封面图片？').then(() => {
                this.form.picture = '';
            });
        },
        submit() {
            const params = {
                resourceType: 1,
                title: this.form.title,
                linkContent: this.form.linkContent,
                labels: this.form.tagList.map(label => label.labelId).join(','),
                imageUrl: this.form.picture
            };
            if (this.id) {
                params.resourceId = this.id;
            }
            if (params.title.length < 1) {
                this.$alert('请输入链接标题');
                return;
            } if (params.title.length > 30) {
                this.$alert('链接标题的长度超过了30,请适当减少');
                return;
            }
            if (String(params.linkContent).trim().length < 1) {
                this.$alert('请编辑详情内容');
                return;
            }
            if (!params.imageUrl) {
                this.$alert('必须上传封面才能保存');
                return;
            }
            this.$loading({
                text: '保存数据中...'
            });
            this.$post('/resources/addOrModifyResource.json', params).then(() => {
                this.$loading().close();
                this.$alert('保存成功').then(() => {
                    this.back();
                });
            }).finally(() => {
                this.$loading().close();
            });
        },
        back() {
            this.$router.push('/marketing/list?type=ad');
        }
    }

};
</script>

<style scoped>
    .back-Btn{
        float: right;
    }
    .picture{
        display: flex;
        align-items: center;
        justify-content: start;
    }
    .picture2{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .picture-delete{
        height: 40px;
        margin-left: 20px;
    }
    .picture-max{
        max-height: 300px;
        max-width: 500px;
    }
    .avatar-uploader {
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader-icon:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;

        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
    .error-text{
        color: red;
    }
    .blue-text{
        color: blue;
    }
</style>
