<template>
    <div v-if="visible" class="mask" @click="triggerHidden">
        <div v-if="srcType === 'link'" class="device">
            <div class="device-content">
                <div id="iwindow">
                    <iframe :src="src" width="320" height="569" frameborder="0"></iframe>
                </div>
            </div>
        </div>
        <video v-if="srcType === 'video'" :src="src" controls class="video"></video>
        <img v-if="srcType === 'image'" :src="src" class="image" />
    </div>
</template>

<script>
export default {
    name: 'preview',
    props: {
        src: {
            type: String,
            validator(value) {
                return value !== undefined;
            }
        },
        visible: {
            type: Boolean,
            default: false
        },
        srcType: {
            type: String,
            validator(value) {
                return ['link', 'video', 'image'].indexOf(value) > -1;
            }
        }
    },
    methods: {
        triggerHidden() {
            this.$emit('hidden');
        }
    }
};
</script>

<style scoped>
    *{
        padding: 0;
        margin: 0;
    }
.mask {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1000;
  width: auto;
  height: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.8);
}
.device {
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  display: block;
  width: 395px;
  height: 813px;
  font-family: "Helvetica Neue", sans-serif;
  background-image: url("./images/iphone-black.png");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: 0 0;
  transition: background-image 0.1s linear;
}
.device .device-content {
  position: absolute;
  top: 117px;
