<template>
    <el-popover
            ref="popover"
            :trigger="trigger"
            :open-delay="openDelay"
            :title="title"
            :disabled="disabled"
            :content="content"
            :reference="reference"
            :popper-class="popperClass"
            :width="width"
            :visible-arrow="visibleArrow"
            :arrow-offset="arrowOffset"
            :transition="transition"
            @show="_show"
            @after-enter="_afterEnter"
            @hide="_hide"
            @after-leave="_afterLeave">
        <div class="search-layer">
            <div class="search-header">
                <div class="search-icon-wrapper">
                    <i class="el-icon-search"></i>
                </div>
                <div class="search-input-wrapper">
                    <el-input v-model="searchKey" :placeholder="placeHolder"></el-input>
                </div>
            </div>
            <div class="search-content">
                <el-table
                        highlight-current-row
                        :show-header="showHeader"
                        :data="renderData"
                        v-loading="isSearching"
                        @row-dblclick="_dbClick">
                    <slot name="list"></slot>
                </el-table>
            </div>
        </div>
        <div style="display: inline-block;" slot="reference">
            <slot name="icon"></slot>
        </div>
    </el-popover>
</template>

<script>
export default {
    name: 'pick-list-popover',
    props: {
        placeHolder: {
            type: String,
            default: '请输入内容'
        },
        showHeader: {
            type: Boolean,
            default: false
        },
        data: {
            type: Array,
            default: () => []
        },
        searchFunc: {
            type: Function,
            default: () => Promise.resolve([])
        },
        autoSearch: {
            type: Boolean,
            default: false
        },
        refreshAfterHide: {
            type: Boolean,
            default: false
        },
        debounce: {
            type: Number,
            default: 300
        },
        // el-popover原生提供的属性
        trigger: {
            type: String,
            default: 'click',
            validator: value => ['click', 'focus', 'hover', 'manual'].indexOf(value) > -1
        },
        openDelay: {
            type: Number,
            default: 0
        },
        title: String,
        disabled: Boolean,
        content: String,
        reference: {},
        popperClass: String,
        width: {},
        visibleArrow: {
            default: true
        },
        arrowOffset: {
            type: Number,
            default: 0
        },
        transition: {
            type: String,
            default: 'fade-in-linear'
        }
    },
    data() {
        return {
            searchKey: '',
            // 这里为了兼容，既使用props属性从父组件传递数据，又兼容组件内部修改数据
            // 根据Vue官方建议，这里将从外部传递的props属性赋值给renderData
            renderData: this.data,
            isSearching: false
        };
    },
    methods: {
        search(key) {
            if (this.search.busy) return;
            this.search.busy = true;

            this.isSearching = true;
            this.searchFunc(key).then((list) => {
                if (Object.prototype.toString.call(list) === '[object Array]') {
                    this.renderData = list;
                } else {
                    this.renderData = [];
                }
            }).finally(() => {
                this.search.busy = false;
                this.isSearching = false;
            });
        },
        // 以下为组件内部方法，外部不要调用
        _dbClick(row, event) {
            this.$emit('pick', row, event);
        },
        _show() {
            this.$emit('show');
        },
        _afterEnter() {
            this.$emit('after-enter');
        },
        _hide() {
            this.$emit('hide');
        },
        _afterLeave() {
            if (this.refreshAfterHide) this.searchKey = '';

            this.$emit('after-leave');
        }
    },
    mounted() {
        if (this.autoSearch) this.search();
    },
    watch: {
        searchKey() {
            if (!this.timeout) {
                this.timeout = setTimeout(() => {
                    this.search(this.searchKey);
                    this.timeout = undefined;
                }, this.debounce);
            }
        },
        disabled(newValue) {
            if (newValue) {
                this.$refs.popover.showPopper = false;
            }
        }
    }
};
</script>

<style scoped lang="scss">
.search-layer {
    font-size: 0;
    line-height: 0;

    .search-header {
        padding-bottom: 10px;
        border-bottom: 1px solid #dcdfe6;

        .search-icon-wrapper {
            display: inline-block;
            font-size: 20px;
            margin-right: 10px;
            vertical-align: middle;
        }
        .search-input-wrapper {
            display: inline-block;
            font-size: 20px;
            vertical-align: middle;
        }
    }
    .search-content {
        height: 300px;
        overflow-y: auto;
    }
}

.icon-layout {
    flex: 0 0 24px;
    margin-right: 5px;
}
</style>
