<template>
    <div>
        <el-row></el-row>
        <el-row :gutter="20">
            <el-col :span="6">
                <el-input
                        placeholder="请按标题关键字搜索"
                        prefix-icon="el-icon-search"
                        v-model="search.input"
                >
                </el-input>
            </el-col>
            <el-col :span="18">
                使用状态
                <el-select v-model="search.select" placeholder="请选择">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row >
            <el-col :span="4" class="content-text-center">
                内容标签：
            </el-col>
            <el-col :span="20">
                <tagList :labelList="labels" labelType="0"></tagList>
            </el-col>
        </el-row>
        <el-row >
            <el-button type="primary" @click="searchData(1)">查询</el-button>
            <el-button type="danger" @click="add">{{addButtonName}}</el-button>
        </el-row>

        <el-table
                :data="tableData"
                border
                style="width: 100%">

            <el-table-column
                    label="序号"
                    type="index">
            </el-table-column>

            <el-table-column
                    label="内容ID"
                    prop="resourceId">
            </el-table-column>

            <el-table-column
                    label="标题"
                    prop="title">
            </el-table-column>

            <el-table-column
                    label="内容"
                    class="hand"
                    width="180">
                <template slot-scope="scope">
                    <template v-if="scope.row.resourceType === 1 || scope.row.resourceType === 4">
                        <div @click="showPreview(scope.row.linkUrl+'&preview=1','link')" class="link hand">{{filterLink(scope.row.linkUrl)}}</div>
                    </template>
                    <template v-if="scope.row.resourceType === 2">
                        <video @click="showPreview(scope.row.vedioUrl,'video')" class="table-video hand" :src="scope.row.vedioUrl"></video>
                    </template>
                    <template v-if="scope.row.resourceType === 3">
                        <img @click="showPreview(scope.row.imageUrl,'image')" class="table-img hand" :src="scope.row.imageUrl"/>
                    </template>
                </template>
            </el-table-column>

            <el-table-column
                    v-if="type === 'video'"
                    label="时长"
                    prop="vedioLength">
                <template slot-scope="scope">
                    {{filterTime(scope.row.vedioLength)}}
                </template>
            </el-table-column>

            <el-table-column
                    label="创建时间">
                <template slot-scope="scope">
                    {{filterDate(scope.row.createTime)}}
                </template>
            </el-table-column>

            <el-table-column
                    label="更新时间">
                <template slot-scope="scope">
                    {{filterDate(scope.row.lastUpdateTime)}}
                </template>
            </el-table-column>

            <el-table-column
                    label="使用状态">
                <template slot-scope="scope">
                    {{ scope.row.isUsed == 0 ? '未使用' : '已使用' }}
                </template>
            </el-table-column>

            <el-table-column label="操作" width="200">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.isUsed !== 0"
                            size="mini"
                            @click="handleView(scope.row.resourceId)">查看</el-button>

                    <el-button
                            v-if="scope.row.isUsed === 0"
                            size="mini"
                            @click="handleEdit(scope.row.resourceId)">编辑</el-button>
                    <el-button
                            v-if="scope.row.isUsed === 0"
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row.resourceId)">删除</el-button>
                </template>
            </el-table-column>

        </el-table>

        <el-row class="footer-pagination">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page="page.current"
                    layout="total, prev, pager, next, jumper"
                    :total="page.total">
            </el-pagination>
        </el-row>
        <preview :src="src" :visible="visible" :srcType="srcType" @hidden="hiddenPreview"></preview>
    </div>
</template>

<script>
import DateUtil from '@/utils/date';
import tagList from './tagList';

const options = [{
    value: -1,
    label: '全部'
}, {
    value: 1,
    label: '已使用'
}, {
    value: 0,
    label: '未使用'
}];

export default {
    name: 'adListSearch',
    components: {
        tagList
    },
    props: {
        search: {
            validator(value) {
                return value.searchValue !== undefined && value.select !== undefined;
            }
        },
        addButtonName: {
            type: String,
            required: true
        },
        type: {
            validator(value) {
                return ['ad', 'picture', 'video'].indexOf(value) > -1;
            }
        },
    },
    data() {
        return {
            options,
            labels: [],
            tableData: [],
            page: {
                current: 1,
                total: 0
            },
            src: '',
            visible: false,
            srcType: 'link',
        };
    },
    created() {
        this.searchData();
    },
    methods: {
        searchData(number = 1) {
            const params = {
                resourceType: this.search.type, // 资源类型，链接(1)，视频(2)，图文(3) ,问卷（4）
                title: this.search.input, // 媒体标题
                isUsed: this.search.select, // 资源使用状态，未使用(0) 【default】 已使用(1)，全部（-1）
                labels: this.labels.map(label => label.labelId).join(','), // 标签字符串（逗号隔空）：只包含标签id
                page: number, // 当前页码
                pageSize: 10, // 每页大小
            };
            this.$loading({
                text: '获取数据中...'
            });
            this.$get('/resources/findResourcesByType.json', params).then((res) => {
                if (res.data.object.list.length === 0 && res.data.object.paging.page !== 1) {
                    // 非第一页 而数量为0条时，跳转到第一页
                    this.searchData(1);
                    return;
                }
                this.$loading().close();
                this.tableData = res.data.object.list;
                this.page.current = res.data.object.paging.page;
                this.search.totle = res.data.object.paging.total;
                this.page.total = res.data.object.paging.total;
            });
        },
        add() {
            this.$router.push(`/marketing/${this.type}/edit`);
        },
        handleCurrentChange(pageNumber) {
            this.searchData(pageNumber);
        },
        handleView(id) {
            this.$router.push(`/marketing/${this.type}/edit?action=view&id=${id}`);
        },
        handleEdit(id) {
            this.$router.push(`/marketing/${this.type}/edit?action=edit&id=${id}`);
        },
        handleDelete(resourceId) {
            this.$confirm('确定要这条数据吗?').then(() => {
                this.$loading({
                    text: '删除数据中...'
                });
                const param = { resourceId };
                this.$post('/resources/deleteResourcesById.json', param).then(() => {
                    this.$loading().close();
                    this.searchData(this.page.current);
                });
            });
        },
        hiddenPreview() {
            this.src = '';
            this.visible = false;
        },
        showPreview(src, type) {
            this.src = src;
            this.srcType = type;
            this.visible = true;
        },
        filterLink(link) {
            let str = '';
            if (link && link.slice) {
                str = `${link.slice(0, -6)}******`;
            }
            return str;
        },
        filterTime(second = 0) {
            let dateStr = '';
            second = Number(second);
            if (!Number.isNaN(second)) {
                const mins = Math.floor(second / 60);
                const seconds = Math.ceil(second % 60);
                dateStr = `${mins} ： ${seconds > 10 ? seconds : `0${seconds}`} `;
            }
            return dateStr;
        },
        filterDate(milseconds) {
            let dateStr = '';
            if (milseconds > 0) {
                dateStr = DateUtil.formatDate(new Date(milseconds), 'yyyy-MM-dd hh:mm:ss');
            }
            return dateStr;
        }
    }
};
</script>

<style scoped>
    .el-row {
        margin-bottom: 20px;
        padding-left: 20px;
    }
    .content-text-center{
        line-height: 42px;
        width: 74px;
    }
    .footer-pagination{
        margin-top: 20px;
        text-align: right;
    }
    .table-video{
        width: 100%;
        background-color: black;
    }
    .table-img{
        width: 100%;
        max-width: 300px;
    }
    .hand{
        cursor: pointer;
    }
    .link{
        text-decoration: underline;
        color: blue;
        text-decoration-color: blue;
    }
</style>
