<template>
    <div class="box">
        <div class="login-box">
            <h1>登录</h1>
            <div class="item">
                <el-input placeholder="请输入用户名"
                          v-model="account">
                    <template slot="prepend">用户:</template>
                </el-input>
            </div>
            <div class="item">
                <el-input type="password"
                          placeholder="请输入密码"
                          v-model="password"
                          @keyup.13.native="login()">
                    <template slot="prepend">密码:</template>
                </el-input>
            </div>
            <div class="item">
                <el-button type="success"
                           @click="login()">登录</el-button>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src

export default {
    name: 'login',
    components: {
    },
    data() {
        return {
            account: '',
            password: '',
        };
    },
    methods: {
        login() {
            if (!this.account) return this.$message.error('请输入用户名');
            if (!this.password) return this.$message.error('请输入密码');
            this.$loading('登录中...');
            this.$get('/login.json', {
                account: this.account,
                password: this.password,
            }).then(() => {
                this.$router.push('/index');
                this.$loading().close();
            }).catch(() => this.$loading().close());
            return '';
        },
    },
};
</script>
<style lang="scss" scoped>
.box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 700px;
    text-align: center;
    .login-box {
        width: 500px;
        .item {
            height: 60px;
        }
    }
}
</style>
