<template>
    <div class="list-container">
        <div class="search-header">
            <el-form :inline="true">
                <el-form-item>
                    <el-input
                        class="round-input"
                        v-model="searchKey"
                        placeholder="请按标题关键字搜索"
                        prefix-icon="el-icon-search">
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="searchByKey">查询</el-button>
                </el-form-item>
                <el-form-item v-if="btnName" style="float: right;">
                    <el-button type="danger" @click="newResource">{{btnName}}</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="search-list">
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    highlight-current-row
                    @current-change="handleCurrentChange"
                    v-loading="isSearching"
                    style="width: 100%">
                <el-table-column
                        label="内容ID"
                        property="resourceId">
                </el-table-column>
                <el-table-column
                        label="标题"
                        property="title">
                </el-table-column>
                <el-table-column
                        label="内容">
                    <template slot-scope="scope">
                        <template v-if="scope.row.resourceType === 1">
                            {{shortcutLinkUrl(scope.row.linkUrl)}}
                        </template>
                        <template v-if="scope.row.resourceType === 2">
                            <div class="video-wrapper">
                                <video class="table-video" :src="scope.row.vedioUrl" controls></video>
                                <div class="video-mask">
                                    <span class="video-duration">{{videoLength(scope.row.vedioLength)}}</span>
                                </div>
                            </div>
                        </template>
                        <template v-if="scope.row.resourceType === 3">
                            <img class="table-img" :src="scope.row.imageUrl"/>
                        </template>
                        <template v-if="scope.row.resourceType === 4">
                            {{shortcutLinkUrl(scope.row.linkUrl)}}
                        </template>
                    </template>
                </el-table-column>
                <el-table-column label="创建时间">
                    <template slot-scope="scope">
                        {{timeFormat(scope.row.createTime)}}
                    </template>
                </el-table-column>
                <el-table-column label="更新时间">
                    <template slot-scope="scope">
                        {{timeFormat(scope.row.lastUpdateTime)}}
                    </template>
                </el-table-column>
            </el-table>
            <el-row class="footer-pagination">
                <el-pagination
                        @current-change="pageChange"
                        :current-page="paging.page"
                        :page-sizes="[paging.pageSize]"
                        :page-size="paging.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="paging.total">
                </el-pagination>
            </el-row>
        </div>
    </div>
</template>

<script>
import dateUtil from '../../../utils/date';
import { utils } from '../mixin/utils';

const FIND_RESOURCES_BY_TYPE = '/resources/findResourcesByType.json';

export default {
    name: 'searchList',
    mixins: [utils],
    data() {
        return {
            currentRow: {},
            tableData: [],
            searchKey: '',
            paging: {
                page: 1, // 当前页码
                pageSize: 10, // 每页显示的条目数
                total: 0 // 总条目数
            },
            isSearching: false
        };
    },
    props: {
        resourceType: {
            type: Number,
            default: 1
        },
        btnName: {
            type: String,
            default: ''
        }
    },
    methods: {
        handleCurrentChange(val) {
            this.currentRow = val;
            this.$emit('select', val);
        },
        searchParam() {
            const { paging: { page, pageSize }, resourceType } = this;
            const params = { page, pageSize, resourceType };
            if (this.searchKey) params.title = this.searchKey;
            return params;
        },
        search() {
            if (this.search.busy) return;
            this.search.busy = true;

            this.isSearching = true;
            this.$get(FIND_RESOURCES_BY_TYPE, this.searchParam()).then((res) => {
                const { list, paging } = res.data.object;
                this.tableData = list;
                this.paging.total = paging.total;
            }).finally(() => {
                this.search.busy = false;
                this.isSearching = false;
            });
        },
        searchByKey() {
            this.paging.page = 1;
            // 每次搜索资源的时候，需要清空上次选中的资源
            // 同时还要通知父组件，清空资源
            this.$emit('clear-selected', this.currentRow);
            this.currentRow = {};
            this.search();
        },
        pageChange(currentPage) {
            this.paging.page = currentPage;
            this.search();
        },
        timeFormat(timestamp) {
            if (timestamp) {
                return dateUtil.formatDate(new Date(timestamp), 'yyyy-MM-dd hh:mm:ss');
            }

            return '';
        },
        newResource() {
            switch (this.resourceType) {
                case 1:
                    this.$router.push('/marketing/ad/edit');
                    break;
                case 2:
                    this.$router.push('/marketing/video/edit');
                    break;
                case 3:
                    this.$router.push('/marketing/picture/edit');
                    break;
                case 4:
                    this.$router.push('/question/detail');
                    break;
                default:
                    break;
            }
        }
    },
    mounted() {
        this.search();
    }
};
</script>

<style scoped lang="scss">
.list-container {
    padding: 0 20px;

    .search-header {}

    .search-list {
        .footer-pagination {
            margin-top: 20px;
            text-align: right;
        }
    }
}

.table-img {
    width: 100%;
}
.video-wrapper {
    position: relative;
    font-size: 0;

    .table-video {
        width: 100%;
        background-color: black;
    }

    .video-mask {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;

        .video-duration {
            position: absolute;
            right: 5px;
            top: 5px;
            font-size: 14px;
            color: white;
            line-height: 1.4;
        }
    }
}
</style>
