<template>
    <div class="container">
        <div class="header">
            <el-row>
                <el-col :span="2">
                    <span>新建</span>
                </el-col>
                <el-col :span="22">
                    <div style="float: right;">
                        <el-button
                            size="small"
                            type="primary"
                            icon="el-icon-back"
                            @click="back">返回</el-button>
                    </div>
                </el-col>
            </el-row>
        </div>
        <el-form
            class="form-container"
            ref="addTask"
            label-position="left"
            :model="form"
            :rules="rules"
            label-width="100px">
            <el-form-item label="文字：">
                <el-input v-model="form.taskTitle" placeholder="请描述将要发送内容的说明，增加前端用户体验">
                    <template slot="append">
                        <span>{{form.taskTitle.length}}</span>/<span>30</span>
                    </template>
                </el-input>
            </el-form-item>
            <el-form-item
                label="内容类型："
                prop="resourceId">
                <el-button type="primary" @click="selectFromLib" :disabled="isSelectResource">从内容库选择</el-button>
                <span class="select-tip">目前只支持单选内容。</span>
                <div class="resource-container" v-if="selectedResource">
                    <template v-if="selectedResource.resourceType === 1 || selectedResource.resourceType === 4">
                        <div class="resource">
                            <a
                                class="resource-url"
                                href="javascript:void(0)"
                                @click="showPreview"
                                >{{shortcutLinkUrl(selectedResource.linkUrl)}}</a>
                        </div>
                    </template>
                    <template v-else-if="selectedResource.resourceType === 2">
                        <div class="resource" @click="showPreview">
                            <video
                                class="video-max"
                                controls
                                preload="auto"
                                :src="selectedResource.vedioUrl"
                                alt="" >
                            </video>
                            <div class="video-mask" v-if="!previewVisible">
                                <span class="video-duration">{{videoLength(selectedResource.vedioLength)}}</span>
                            </div>
                        </div>
                    </template>
                    <template v-else>
                        <div class="resource">
                            <img
                                class="picture-max"
                                :src="selectedResource.imageUrl"
                                @click="showPreview"
                                alt="">
                        </div>
                    </template>
                    <div class="delete-btn-wrapper">
                        <el-button
                            class="delete-btn"
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            @click="deleteResource"></el-button>
                    </div>
                </div>
            </el-form-item>
            <el-form-item
                label="用户标签：">
                <label-picker
                    :searchFunc="labelSearch"
                    :labels="labels"
                    :auto-search="Boolean(true)"
                    :refresh-after-hide="Boolean(true)"
                    @pickLabel="pickLabel"
                    @deleteLabel="deleteLabel">
                </label-picker>
            </el-form-item>
            <el-form-item
                label="渠道分类："
                prop="channelCategory">
                <el-select v-model="form.channelCategory" placeholder="请选择">
                    <el-option label="个人号" value="0"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item
                label="渠道类型："
                prop="channels">
                <label-picker
                    :searchFunc="channelSearch"
                    :labels="channels"
                    :auto-search="Boolean(true)"
                    :refresh-after-hide="Boolean(true)"
                    :disabled="isChannelSelectDisabled"
                    @pickLabel="channelPick"
                    @deleteLabel="deleteChannel">
                </label-picker>
            </el-form-item>
            <el-form-item style="margin-top: 10px;">
                <el-button type="danger" @click="formValidate('addTask')">实时发送</el-button>
                <el-button @click="formValidate('addTask', true)">定时发送</el-button>
            </el-form-item>
        </el-form>

        <!--内容库弹框-->
        <el-dialog
            title="选择内容"
            :visible.sync="contentSelectDialogVisible"
            width="60%">
            <content-libiary
                @select-confirm="selectConfirm"
                @cancel-select="contentSelectDialogVisible = false"></content-libiary>
        </el-dialog>

        <!--发送任务确定弹框-->
        <el-dialog
                title="定时群发"
                :visible.sync="sendTaskDialogVisible"
                width="30%">
            <p style="font-size: 16px; margin-bottom: 20px;">请选择时间：</p>
            <el-form
                ref="timeForm"
                label-position="left"
                label-width="70px"
                :model="timeForm"
                :rules="timeRules">
                <el-form-item label="日期：" prop="sendDate">
                    <el-date-picker
                        v-model="timeForm.sendDate"
                        type="date"
                        placeholder="选择日期"
                        :picker-options="datePickerOptions"
                        @change="dateChange">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="时间：" prop="sendTime">
                    <el-time-picker
                        :disabled="is11After10Min(timeForm.sendDate || new Date())"
                        v-model="timeForm.sendTime"
                        placeholder="选择时间"
                        :picker-options="timePickerOptions">
                    </el-time-picker>
                </el-form-item>
                <el-form-item>
                    <p>最早发送时间为当前时间的10分钟后。</p>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="sendTaskDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="performSendScheduled('timeForm')">确 定</el-button>
            </span>
        </el-dialog>

        <preview
            :visible="previewVisible"
            :srcType="previewData.type"
            :src="previewUrl(previewData.url, previewData.type)"
            @hidden="hidePreview"></preview>

    </div>
</template>

<script>
import preview from '../../components/preview';

import { $get } from '../../utils/request';
import timeHandler from '../../utils/date';
import { utils } from './mixin/utils';

import contentLibiary from './components/contentLibrary';

const LABEL_SEARCH = '/crm/label/search.json';
const FIND_TASK_CONDITION = '/task/findTaskCondition.json';
const ADD_TASK = '/task/addTask.json';

const HOUR_MILLISECOND = 60 * 60 * 1000;
const MINUTE_MILLISECOND = 60 * 1000;
const SECOND_MILLISECOND = 1000;
const MINUTE_THRESHOLD = 10; // 差这么多分钟到晚上11点时，这一天也不能被选中

const searchFuncFactory = (reqHandler, url, resHandler) => (key) => {
    const params = reqHandler(key);

    return $get(url, params).then((res) => {
        const { object } = res.data;
        if (object && object.length) {
            resHandler(object);

            return Promise.resolve(object);
        }

        return Promise.resolve(object);
    });
};

export default {
    name: 'groupMsgEdit',
    mixins: [utils],
    data() {
        return {
            form: {
                taskTitle: '',
                resourceId: '',
                labels: '',
                channelCategory: '',
                channels: ''
            },
            rules: {
                resourceId: [
                    {
                        required: true, message: '请选择内容类型', trigger: 'blur'
                    }
                ],
                channelCategory: [
                    {
                        required: true, message: '请选择渠道类型', trigger: 'blur'
                    }
                ],
                channels: [
                    {
                        required: true, message: '请选择渠道类型', trigger: 'blur'
                    }
                ]
            },
            timeRules: {
                sendDate: [
                    {
                        required: true, message: '请选择发送日期', trigger: 'blur'
                    }
                ],
                sendTime: [
                    {
                        required: true, message: '请选择发送时间', trigger: 'blur'
                    }
                ]
            },
            labels: [],
            channels: [],
            timeForm: {
                sendDate: '',
                sendTime: ''
            },
            datePickerOptions: {
                disabledDate: () => true,
            },
            timePickerOptions: {
                selectableRange: ''
            },
            sendTaskDialogVisible: false,
            contentSelectDialogVisible: false,
            previewVisible: false,
            selectedResource: ''
        };
    },
    components: {
        contentLibiary,
        preview
    },
    methods: {
        selectFromLib() {
            this.contentSelectDialogVisible = true;
        },
        labelSearch: searchFuncFactory((key) => {
            const params = {
                labelType: 1, // 表示自动标签
                labelPartitions: '1,3', // 1表示用户标签
                labelGroupId: 0 // 暂时传0
            };
            if (key) {
                params.labelName = key;
            }

            return params;
        }, LABEL_SEARCH, (list) => {
            list.forEach((item) => {
                item.closable = true;
            });
            return list;
        }, this),
        pickLabel(label) {
            if (label) {
                if (this.findLabelIndex(label) === -1) {
                    this.labels.push(label);
                }
            }
        },
        deleteLabel(label) {
            if (label) {
                const index = this.findLabelIndex(label);
                if (index > -1) {
                    this.labels.splice(index, 1);
                }
            }
        },
        findLabelIndex(label) {
            return this.labels.findIndex(item => item.labelId === label.labelId);
        },
        channelSearch: searchFuncFactory((key) => {
            const params = {};
            if (key) {
                params.nickName = key;
            }
            return params;
        }, FIND_TASK_CONDITION, (list) => {
            list.forEach((item) => {
                item.closable = true;
                item.labelName = item.nickName;
            });
            return list;
        }),
        channelPick(channel) {
            if (channel) {
                if (this.findChannelIndex(channel) === -1) {
                    this.channels.push(channel);
                }
            }
        },
        deleteChannel(channel) {
            if (channel) {
                const index = this.findChannelIndex(channel);
                if (index > -1) {
                    this.channels.splice(index, 1);
                }
            }
        },
        findChannelIndex(channel) {
            return this.channels.findIndex(item => item.wechatId === channel.wechatId);
        },
        sendPramHandle(isScheduled) {
            const params = this.form;
            if (isScheduled && this.timeForm.sendDate && this.timeForm.sendTime) {
                params.cronTime = `${timeHandler.formatDate(this.timeForm.sendDate, 'yyyy-MM-dd')} ${timeHandler.formatDate(this.timeForm.sendTime, 'hh:mm:ss')}`;
            }
            if (this.labels && this.labels.length) {
                const labelIds = this.labels.map(label => label.labelId);
                params.labels = labelIds.join(',');
            } else {
                params.labels = '';
            }
            return params;
        },
        handleChannels(channels) {
            if (channels && channels.length) {
                const tempList = [];
                channels.forEach((channel) => {
                    tempList.push({
                        wechatId: channel.wechatId,
                        channelId: channel.channelId,
                        nickName: channel.nickName
                    });
                });

                return JSON.stringify(tempList);
            }

            return '';
        },
        formValidate(formName, isScheduled) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (isScheduled) {
                        this.sendScheduled();
                    } else {
                        this.sendImmediately();
                    }
                }

                return false;
            });
        },
        sendImmediately() {
            if (this.sendImmediately.busy) return;
            this.sendImmediately.busy = true;

            this.$confirm(
                '是否确认发送，确认后系统立刻发送，请谨慎操作。',
                '实时发送',
                {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }
            ).then(this.sendTask.bind(this, false)).finally(() => {
                this.sendImmediately.busy = false;
                this.$loading().close();
            });
        },
        getDateAfter10Min(time) {
            time.setMinutes(time.getMinutes() + 10);
            return time;
        },
        // 距离明天是否少于10分钟
        is11After10Min(time) {
            if (time) {
                const timeCopy = new Date(time.getTime());
                const cuttedTime = this.cutTime(timeCopy);
                cuttedTime.setHours(22);

                return (cuttedTime.getTime() - time.getTime()) / (MINUTE_MILLISECOND) < MINUTE_THRESHOLD;
            }

            return false;
        },
        getSelectableRange(time) {
            if (time) {
                const now = new Date();
                if (time.getTime() <= now.getTime()) {
                    return `${timeHandler.formatDate(this.getDateAfter10Min(now), 'hh:mm:ss')} - 22:59:59`;
                }

                return '07:00:00 - 22:59:59';
            }

            return '07:00:00 - 22:59:59';
        },
        cutTime(time) {
            const copyTime = new Date(time.getTime());
            const hour = copyTime.getHours();
            const min = copyTime.getMinutes();
            const sec = copyTime.getSeconds();
            const miliSec = copyTime.getMilliseconds();
            copyTime.setTime(
                copyTime.getTime()
                - hour * HOUR_MILLISECOND
                - min * MINUTE_MILLISECOND
                - sec * SECOND_MILLISECOND - miliSec
            );
            return copyTime;
        },
        sendScheduled() {
            const now = new Date();
            const cuttedTime = this.cutTime(now);
            this.datePickerOptions.disabledDate = (time) => {
                if (time.getTime() < cuttedTime.getTime()) {
                    return true;
                }
                if (time.getTime() === cuttedTime.getTime()) {
                    return this.is11After10Min(now);
                }

                return false;
            };
            this.timePickerOptions.selectableRange = this.getSelectableRange(now);

            this.sendTaskDialogVisible = true;
        },
        performSendScheduled(formName) {
            if (this.performSendScheduled.busy) return;
            this.performSendScheduled.busy = true;

            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.sendTask(true).finally(() => {
                        this.performSendScheduled.busy = false;
                        this.$loading().close();
                    });
                }

                return false;
            });
        },
        sendTask(isScheduled) {
            this.$loading();
            return this.$post(ADD_TASK, this.sendPramHandle(isScheduled)).then(() => {
                this.$router.push('/groupMsg/list');
                this.$loading().close();
            });
        },
        dateChange(time) {
            this.timePickerOptions.selectableRange = this.getSelectableRange(time);
        },
        selectConfirm(resources) {
            this.contentSelectDialogVisible = false;
            this.selectedResource = resources[0];
            this.form.resourceId = resources[0].resourceId;
        },
        getResourceTypeName() {
            switch (this.selectedResource.resourceType) {
                case 1:
                    return '链接';
                case 2:
                    return '视频';
                case 3:
                    return '图片';
                case 4:
                    return '问卷';
                default:
                    return '';
            }
        },
        deleteResource() {
            this.$confirm(`是否删除${this.getResourceTypeName()}？`).then(() => {
                this.selectedResource = '';
            });
        },
        showPreview() {
            this.previewVisible = true;
        },
        hidePreview() {
            this.previewVisible = false;
        },
        back() {
            this.$router.back();
        }
    },
    computed: {
        previewData() {
            const { selectedResource } = this;
            const result = {};
            let url = '';
            let type = '';
            switch (selectedResource.resourceType) {
                case 1:
                case 4:
                    url = selectedResource.linkUrl;
                    type = 'link';
                    console.log('link');
                    break;
                case 2:
                    url = selectedResource.vedioUrl;
                    type = 'video';
                    break;
                case 3:
                    url = selectedResource.imageUrl;
                    type = 'image';
                    break;
                default:
                    break;
            }

            result.url = url;
            result.type = type;

            return result;
        },
        isSelectResource() {
            return !!this.selectedResource;
        },
        isChannelSelectDisabled() {
            return this.channels.length > 0;
        }
    },
    watch: {
        'form.taskTitle': function (newValue) {
            if (newValue.length > 30) {
                this.$nextTick(() => {
                    this.form.taskTitle = newValue.substring(0, 30);
                });
            }
        },
        channels(newValue) {
            this.form.channels = this.handleChannels(newValue);
        }
    }
};
</script>

<style scoped lang="scss">
.form-container {
    max-width: 900px;
}

.select-tip {
    margin-left: 10px;
}

.resource-container {
    display: flex;
    margin-top: 20px;
    font-size: 0;
    line-height: 0;
    align-items: center;

    .resource {
        position: relative;
        margin-right: 40px;

        .resource-url {
            font-size: 14px;
            line-height: 1.4;
            word-break: break-all;
        }

        .video-mask {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            background: rgba(0, 0, 0, 0.6);

            .video-duration {
                position: absolute;
                right: 5px;
                top: 5px;
                font-size: 14px;
                color: white;
                line-height: 1.4;
            }
        }
    }
    .delete-btn-wrapper {
        flex: 0 0 40px;
        display: inline-block;
    }
    .delete-btn {
        height: 40px;
        margin-left: 20px;
    }

    .video-max {
        max-height: 300px;
        max-width: 500px;
    }
    .picture-max {
        max-height: 300px;
        max-width: 500px;
    }
}
</style>
