<template>
    <div class="container">
        <div class="header">
            <el-row>
                <el-col :span="2">
                    <span>详情</span>
                </el-col>
                <el-col :span="22">
                    <div style="float: right;">
                        <el-button
                            size="small"
                            type="primary"
                            icon="el-icon-back"
                            @click="back">返回</el-button>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="content">
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">文字：</el-col>
                <el-col :span="labelContentSpan">{{task.taskTitle}}</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">内容ID：</el-col>
                <el-col :span="labelContentSpan">{{task.resources.resourceId}}</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">标题：</el-col>
                <el-col :span="labelContentSpan">{{task.resources.title}}</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">内容：</el-col>
                <el-col :span="labelContentSpan">
                    <template v-if="task.resources.resourceType === 1 || task.resources.resourceType === 4">
                        <div class="resource">
                            <a
                                class="resource-url"
                                href="javascript:void(0)"
                                @click="showPreview"
                            >{{shortcutLinkUrl(task.resources.linkUrl)}}</a>
                        </div>
                    </template>
                    <template v-else-if="task.resources.resourceType === 2">
                        <div class="resources" @click="showPreview">
                            <video
                                class="video-max"
                                controls
                                preload="auto"
                                :src="task.resources.vedioUrl"
                                alt="" >
                            </video>
                            <div class="video-mask" v-if="!previewVisible">
                                <span class="video-duration">{{videoLength(task.resources.vedioLength)}}</span>
                            </div>
                        </div>
                    </template>
                    <template v-else>
                        <div class="resources">
                            <img
                                class="picture-max"
                                :src="task.resources.imageUrl"
                                @click="showPreview"
                                alt="">
                        </div>
                    </template>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">内容类型：</el-col>
                <el-col :span="labelContentSpan">{{resourceType}}</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan"><span class="label-name">用户标签：</span></el-col>
                <el-col :span="labelContentSpan">
                    <ft-labels :labels="task.labels"></ft-labels>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan"><span>渠道分类：</span></el-col>
                <el-col :span="labelContentSpan">{{channelCategory}}</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan" class="label-name">渠道类型：</el-col>
                <el-col :span="labelContentSpan">
                    <ft-labels :labels="task.channels"></ft-labels>
                </el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">发送时间：</el-col>
                <el-col :span="labelContentSpan">{{timeFormat(task.taskSendTime)}}</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">发送成功：</el-col>
                <el-col :span="labelContentSpan">{{task.taskSendSuccess}}人</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">发送失败：</el-col>
                <el-col :span="labelContentSpan">{{task.taskSendFail}}人</el-col>
            </el-row>
            <el-row :gutter="20" class="content-item">
                <el-col :span="labelSpan">发送状态：</el-col>
                <el-col :span="labelContentSpan">{{taskSendStatus}}</el-col>
            </el-row>
        </div>

        <preview
            :visible="previewVisible"
            :srcType="previewData.type"
            :src="previewUrl(previewData.url, previewData.type)"
            @hidden="hidePreview"></preview>

    </div>
</template>

<script>
import dateUtil from '../../utils/date';
import { utils } from './mixin/utils';

const FIND_TASK_BY_ID = '/task/findTaskById.json';

export default {
    name: 'groupMsgDetail',
    mixins: [utils],
    data() {
        return {
            labelSpan: 3,
            labelContentSpan: 21,
            taskId: 0,
            task: {
                resources: {
                    title: '',
                    resourceType: '',
                    imageUrl: '',
                    vedioUrl: '',
                    linkUrl: ''
                }
            },
            previewVisible: false
        };
    },
    methods: {
        findTaskById() {
            this.$loading();
            const { taskId } = this;
            this.$get(FIND_TASK_BY_ID, { taskId }).then((res) => {
                this.task = res.data.object;

                this.task.channels.forEach((channel) => {
                    channel.labelName = channel.nickName;
                });
            }).finally(() => {
                this.$loading().close();
            });
        },
        back() {
            this.$router.back();
        },
        showPreview() {
            this.previewVisible = true;
        },
        hidePreview() {
            this.previewVisible = false;
        },
        timeFormat(timestamp) {
            if (timestamp) {
                return dateUtil.formatDate(new Date(timestamp), 'yyyy-MM-dd hh:mm:ss');
            }

            return '';
        }
    },
    computed: {
        resourceType() {
            switch (this.task.resources.resourceType) {
                case 1:
                    return '链接';
                case 2:
                    return '视频';
                case 3:
                    return '图片';
                case 4:
                    return '问卷';
                default:
                    return '';
            }
        },
        channelCategory() {
            switch (this.task.channelCategory) {
                case 0:
                    return '个人号';
                default:
                    return '';
            }
        },
        taskSendStatus() {
            switch (this.task.taskSendStatus) {
                case 0:
                    return '未发送';
                case 1:
                    return '发送中';
                case 2:
                    return '已发送';
                default:
                    return '';
            }
        },
        previewData() {
            const { resources } = this.task;
            const result = {};
            let url = '';
            let type = '';
            switch (resources.resourceType) {
                case 1:
                case 4:
                    url = resources.linkUrl;
                    type = 'link';
                    break;
                case 2:
                    url = resources.vedioUrl;
                    type = 'video';
                    break;
                case 3:
                    url = resources.imageUrl;
                    type = 'image';
                    break;
                default:
                    break;
            }

            result.url = url;
            result.type = type;

            return result;
        }
    },
    created() {
        this.taskId = this.$route.params.taskId;
    },
    mounted() {
        this.findTaskById();
    },
};
</script>

<style scoped lang="scss">
.container {
    width: 60%;
}

.content {
    .content-item {
        font-size: 16px;
        margin-bottom: 22px;

        .label-name {
            line-height: 42px;
        }
    }

    .resources {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        font-size: 0;

        .resource-url {
            font-size: 14px;
            line-height: 1.4;
        }

        .video-mask {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            background: rgba(0, 0, 0, 0.6);

            .video-duration {
                position: absolute;
                right: 5px;
                top: 5px;
                font-size: 14px;
                color: white;
                line-height: 1.4;
            }
        }
    }

    .video-max {
        max-height: 300px;
        max-width: 500px;
    }
    .picture-max {
        max-height: 300px;
        max-width: 500px;
    }
}
</style>
