function format(num) {
    // 去掉前面的0，保留两位
    return num.substring(num.length - 2, num.length);
}
function returnDate(time, type) {
    if (!time) return '';
    const date = new Date(time);
    const y = 1900 + date.getYear(); // 获取年
    const m = `0${date.getMonth() + 1}`; // 获取月
    const d = `0${date.getDate()}`; // 获取日
    const h = `0${date.getHours()}`; // 获取时
    const min = `0${date.getMinutes()}`; // 获取分
    const s = `0${date.getSeconds()}`; // 获取秒
    const date1 = `${y}-${format(m)}-${format(d)}`; // 日期格式
    const date2 = `${format(h)}:${format(min)}:${format(s)}`; // 时分秒格式
    // 传一个类型，若为‘hour’，则输出值带时分秒（默认不带）
    const date3 = type === 'hour' ? `${date1} ${date2}` : date1;
    return date3;
}

export default {
    returnDate
};
