<template>
    <div>
        <el-tag class="label"
                v-for="(label, index) in labels"
                :key="index"
                :type="label.type"
                :closable="label.closable"
                :disable-transitions="label.disableTransitions"
                :hit="label.hit"
                :color="label.color"
                :size="label.size"
                @close="_close(index)">
            {{label.labelName}}
        </el-tag>
    </div>
</template>

<script>
export default {
    name: 'ft-labels',
    props: {
        labels: {
            type: Array,
            default: () => []
        }
    },
    methods: {
        _close(index) {
            this.$emit('deleteLabel', index, this.labels);
        }
    }
};
</script>

<style scoped lang="scss">
.label {
  margin: 5px;
  vertical-align: middle;

  &:last-child {
    margin-right: 0;
  }
}
</style>
