<template>
    <el-table ref="myTable"
              :style="myStyle"
              :data="data"
              :height="height"
              :max-height="maxHeight"
              :stripe="stripe"
              :border="border"
              :size="size"
              :fit="fit"
              :show-header="showHeader"
              :highlight-current-row="highlightCurrentRow"
              :current-row-key="currentRowKey"
              :row-class-name="rowClassName"
              :row-style="rowStyle"
              :cell-class-name="cellClassName"
              :cell-style="cellStyle"
              :header-row-class-name="headerRowClassName"
              :header-row-style="headerRowStyle"
              :header-cell-class-name="headerCellClassName"
              :header-cell-style="headerCellStyle"
              :row-key="rowKey"
              :empty-text="emptyText"
              :default-expand-all="defaultExpandAll"
              :expand-row-keys="expandRowKeys"
              :default-sort="defaultSort"
              :tooltip-effec="tooltipEffect"
              :show-summary="showSummary"
              :sum-text="sumText"
              :summary-method="summaryMethod"
              :span-method="spanMethod"
              :select-on-indeterminate="selectOnIndeterminate"
              @select="_pickChange"
              @select-all="_pickChange"
              @selection-change="_selectionChange"
              @cell-mouse-enter="_cellMouseEnter"
              @cell-mouse-leave="_cellMouseLeave"
              @cell-click="_cellClick"
              @cell-dblclick="_cellDblClick"
              @row-click="_rowClick"
              @row-contextmenu="_rowContextMenu"
              @row-dblclick="_rowDblClick"
              @header-click="_headerClick"
              @header-contextmenu="_headerContextMenu"
              @sort-change="_sortChange"
              @filter-change="_filterChange"
              @current-change="_currentChange"
              @header-dragend="_headerDragend"
              @expand-change="_expandChange">
        <el-table-column type="selection"
                         width="55"
                         v-if="showSeletion">
        </el-table-column>
        <slot></slot>
    </el-table>
</template>

<script>
const ramda = require('ramda');

export default {
    name: 'FtTable',
    props: {
        data: {
            type: Array,
            default() {
                return [];
            }
        },

        size: String,

        width: [String, Number],

        height: [String, Number],

        maxHeight: [String, Number],

        fit: {
            type: Boolean,
            default: true
        },

        stripe: Boolean,

        border: Boolean,

        rowKey: [String, Function],

        context: {},

        showHeader: {
            type: Boolean,
            default: true
        },

        showSummary: Boolean,

        sumText: String,

        summaryMethod: Function,

        rowClassName: [String, Function],

        rowStyle: [Object, Function],

        cellClassName: [String, Function],

        cellStyle: [Object, Function],

        headerRowClassName: [String, Function],

        headerRowStyle: [Object, Function],

        headerCellClassName: [String, Function],

        headerCellStyle: [Object, Function],

        highlightCurrentRow: Boolean,

        currentRowKey: [String, Number],

        emptyText: String,

        expandRowKeys: Array,

        defaultExpandAll: Boolean,

        defaultSort: Object,

        tooltipEffect: String,

        spanMethod: Function,

        selectOnIndeterminate: {
            type: Boolean,
            default: true
        },

        // 以下是自定义属性
        myStyle: {
            type: String,
            default: 'width: 100%;'
        },
        showSeletion: { // 是否展示复选框
            type: Boolean,
            default: false
        },
        hasItem: {
            type: Function,
            default() {
                return () => false;
            }
        },
        equalItem: {
            type: Function,
            default() {
                return () => false;
            }
        }
    },
    data() {
        return {
            pickList: []
        };
    },
    methods: {
        // 以下为自定义方法
        myToggleRowSelection(condition = this.equalItem) {
            if (this.pickList.length && typeof condition === 'function') {
                this.pickList.forEach((item) => {
                    this.data.forEach((element, index) => {
                        if (condition(element, item)) {
                            this.$nextTick(() => {
                                this.$refs.myTable.toggleRowSelection(this.data[index]);
                            });
                        }
                    });
                });
            } else {
                this.clearSelection();
            }
        },
        getPickedItems() {
            return this.pickList;
        },
        clearPickedItems() {
            this.pickList = [];
        },
        // 以下为原始el-table方法的代理
        clearSelection() {
            this.$refs.myTable.clearSelection();
        },
        toggleRowSelection(row, selected) {
            this.$refs.myTable.toggleRowSelection(row, selected);
        },
        toggleAllSelection() {
            this.$refs.myTable.toggleAllSelection();
        },
        toggleRowExpansion(row, expanded) {
            this.$refs.myTable.toggleRowExpansion(row, expanded);
        },
        setCurrentRow(row) {
            this.$refs.myTable.setCurrentRow(row);
        },
        clearSort() {
            this.$refs.myTable.clearSort();
        },
        clearFilter() {
            this.$refs.myTable.clearFilter();
        },
        doLayout() {
            this.$refs.myTable.doLayout();
        },
        sort(prop, order) {
            this.$refs.myTable.sort(prop, order);
        },
        // 以下为内部方法，外部不要调用
        _pickChange(rows, pickedRow) {
            if (!pickedRow) { // 点击全选
                if (rows.length) { // 勾上
                    rows.forEach((row) => {
                        if (this._findIndexInPickList(row) < 0) this._pushIntoPickList(row);
                    });
                } else { // 勾掉
                    this.data.forEach((item) => {
                        const index = this._findIndexInPickList(item);
                        if (index >= 0) this.pickList.splice(index, 1);
                    });
                }

                this.$emit('select-all', rows, pickedRow);
            } else { // 点击列表
                const index = this._findIndexInPickList(pickedRow);
                if (index < 0) { // 钩上
                    this._pushIntoPickList(pickedRow);
                } else { // 钩掉
                    this.pickList.splice(index, 1);
                }

                this.$emit('select', rows, pickedRow);
            }
        },
        _pushIntoPickList(source) {
            const copy = ramda.clone(source);
            this.pickList.push(copy);
        },
        _findIndexInPickList(target) {
            return this.pickList.findIndex(this.hasItem(target));
        },
        // 以下方法转发原生el-table事件
        _selectionChange(selection) {
            this.$emit('selection-change', selection);
        },
        _cellMouseEnter(row, column, cell, event) {
            this.$emit('cell-mouse-enter', row, column, cell, event);
        },
        _cellMouseLeave(row, column, cell, event) {
            this.$emit('cell-mouse-leave', row, column, cell, event);
        },
        _cellClick(row, column, cell, event) {
            this.$emit('cell-click', row, column, cell, event);
        },
        _cellDblClick(row, column, cell, event) {
            this.$emit('cell-dblclick', row, column, cell, event);
        },
        _rowClick(row, event, column) {
            this.$emit('row-click', row, event, column);
        },
        _rowContextMenu(row, event) {
            this.$emit('row-contextmenu', row, event);
        },
        _rowDblClick(row, event) {
            this.$emit('row-dblclick', row, event);
        },
        _headerClick(column, event) {
            this.$emit('header-click', column, event);
        },
        _headerContextMenu(column, event) {
            this.$emit('header-contextmenu', column, event);
        },
        _sortChange(sordData) {
            this.$emit('sort-change', sordData);
        },
        _filterChange(filters) {
            this.$emit('filter-change', filters);
        },
        _currentChange(currentRow, oldCurrentRow) {
            this.$emit('current-change', currentRow, oldCurrentRow);
        },
        _headerDragend(newWidth, oldWidth, column, event) {
            this.$emit('header-dragend', newWidth, oldWidth, column, event);
        },
        _expandChange(row, expandedRows) {
            this.$emit('expand-change', row, expandedRows);
        }
    },
    watch: {
        data(newData, oldData) {
            // 数据发生变化了
            if (newData !== oldData) {
                console.log('数据发生变化了');
                this.myToggleRowSelection();
            }
        },
        pickList(newPickList) {
            this.$emit('pickListChange', newPickList);
        }
    }
};

</script>

