export const utils = {
    methods: {
        videoLength(seconds) {
            if (seconds) {
                const hour = this.prefixZero(Math.floor(seconds / (60 * 24)));
                const min = this.prefixZero(Math.floor((seconds - hour * 60 * 24) / 60));
                const sec = this.prefixZero(seconds - hour * 60 * 24 - min * 60);

                return `${hour}:${min}:${sec}`;
            }

            return '';
        },
        prefixZero(number) {
            if (!number) return '00';

            const result = `0${number}`;
            if (result.length > 2) {
                return result.slice(1);
            }

            return result;
        },
        previewUrl(url, type) {
            if (url && typeof url === 'string' && type === 'link') {
                return `${url}${url.indexOf('?') > -1 ? '&' : '?'}preview=1`;
            }

            return url;
        },
        shortcutLinkUrl(originalUrl) {
            if (originalUrl) {
                return `${originalUrl.slice(0, -6)}******`;
            }

            return '';
        }
    }
};
