<template>
    <div>
        <div class="header">
            平台用户列表
        </div>
        <div class="content">
            <div class="item-title">
                <span>筛选</span>
                <div class="btn-box">
                    <el-button @click="search()"
                               size="mini">查询结果</el-button>
                </div>
            </div>
            <el-input size="small"
                      v-model="searchText"
                      placeholder="用户编码/姓名/微信号/手机号"
                      @keyup.13.native="search()"></el-input>
            <div class="item-title item-title-level-2">
                <span>数据列表</span>
            </div>
            <el-table ref="multipleTable"
                      :data="dataList"
                      tooltip-effect="dark"
                      style="width: 100%"
                      border>
                <el-table-column prop="userId"
                                 label="用户编码">
                </el-table-column>
                <el-table-column prop="trueName"
                                 label="姓名">
                </el-table-column>
                <el-table-column prop="mobile"
                                 label="手机号">
                </el-table-column>
                <el-table-column prop="wechatId"
                                 label="微信号">
                </el-table-column>
                <el-table-column prop="country"
                                 label="国家">
                </el-table-column>
                <el-table-column prop="serviceUnitAmount"
                                 label="服务商品客单价">
                </el-table-column>
                <el-table-column prop="commonUnitAmount"
                                 label="实物商品客单价">
                </el-table-column>
                <el-table-column prop="registerChannel"
                                 label="注册渠道">
                </el-table-column>
                <el-table-column prop="userOrigin"
                                 label="用户来源">
                </el-table-column>
                <el-table-column label="注册时间">
                    <template slot-scope="scope">
                        {{returnDate(scope.row.createTime)}}
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button @click="checkInfo(scope.row.userId)"
                                   type="text"
                                   size="middle">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="page-box">
                <el-pagination @size-change="pageSizeChange"
                               class="ft-pagination"
                               @current-change="pageChange"
                               :current-page="page"
                               :page-sizes="[10, 20, 50]"
                               :page-size="pageSize"
                               layout="total, sizes, prev, pager, next, jumper"
                               :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import timeHandle from '../../utils/timeHandle.js';

export default {
    name: 'userInfo',
    props: [],
    data() {
        return {
            searchText: '', // 搜索条件
            currentSearchText: '', // 当前列表展示的搜索条件
            page: 1, // 默认初始化第一页
            pageSize: 10, // 每页显示20条
            total: 0, // 所有数据
            dataList: [] // 列表数据，结构如下
            /* {
                    "userId": 1,
                    "trueName": "11",
                    "gender": "F",
                    "birthday": 1541490036000,
                    "babyBirthday": 1541490038000,
                    "nickName": "11",
                    "mobile": "1314567",
                    "avatar": "11",
                    "wechatId": "111",
                    "wechat": "11",
                    "unionId": "11",
                    "openId": "11",
                    "fissionLevel": 1,
                    "fromUserId": 111,
                    "originUserId": 11,
                    "userStatus": 1,
                    "country": "sss",
                    "city": "www",
                    "occupation": "w",
                    "registerChannel": "1",
                    "userOrigin": "1",
                    "idDelete": 1,
                    "createTime": 1541490071000,
                    "updateTime": 1541490076000,
                    "serviceUnitAmount": 100,
                    "commonUnitAmount": 100
           }, */
        };
    },
    created() {
        this.getList();
    },
    methods: {
        // 查询按钮点击
        search() {
            this.currentSearchText = this.searchText;
            this.page = 1; // 每次点击搜索，将页面置为第一页
            this.getList();
        },
        // 获取表格内容
        getList() {
            this.$loading('加载中...');
            this.$get('/statistic/user/list.json', {
                keyword: this.currentSearchText,
                page: this.page,
                pageSize: this.pageSize
            }).then((res) => {
                this.dataList = res.data.object.list;
                this.total = res.data.object.paging.total;
                // 若当前页没有数据，则跳到第一页
                if (!this.dataList.length && this.page !== 1) {
                    this.page = 1;
                    this.getList();
                }
                this.$loading().close();
            }).catch(() => this.$loading().close());
        },
        // 表格页面变更
        pageChange(e) {
            this.page = e;
            this.getList();
        },
        // 表格每页条目数量变更
        pageSizeChange(e) {
            this.pageSize = e;
            this.getList();
        },
        // 跳转至相应用户详情页
        checkInfo(userId) {
            this.$router.push(`/userInfo?userId=${userId}`);
        },
        // 格式化时间处理
        returnDate(time) {
            return timeHandle.returnDate(time, 'hour');
        },
    },
    mounted() {
    }
};
</script>

