<template>
    <div class="home">
        <el-menu class="el-menu-demo"
                 mode="horizontal"
                 background-color="#222"
                 text-color="#fff"
                 active-text-color="#ffd04b">
            <el-menu-item disabled
                          index="top_menu">
                <template>
                    <div class="menu-title"
                         :title="title">{{title}}</div>
                </template>
            </el-menu-item>
            <el-submenu v-for="(menu, index) in menus"
                        :index="String(index)"
                        :key="index">
                <template slot="title">{{menu.label}}</template>
                <el-menu-item v-for="(subMenu, subIndex) in menu.children"
                              :index="`${index}-${subIndex}`"
                              :key="`${index}-${subIndex}`">
                    <router-link class="menu-link"
                                 v-if="subMenu.router"
                                 :to="subMenu.href">{{subMenu.label}}</router-link>
                    <a v-else
                       class="menu-link"
                       :href="subMenu.href">{{subMenu.label}}</a>
                </el-menu-item>
            </el-submenu>
            <el-submenu v-if="moreMenus.length > 0"
                        index="5"
                        key="5">
                <template slot="title">more</template>
                <el-submenu v-for="(menu, index) in moreMenus"
                            :index="String(6+index)"
                            :key="6+index">
                    <template slot="title">{{menu.label}}</template>
                    <el-menu-item v-for="(subMenu, subIndex) in menu.children"
                                  :index="`${index}-${subIndex}`"
                                  :key="`${index}-${subIndex}`">
                        <router-link class="menu-link"
                                     v-if="subMenu.router"
                                     :to="subMenu.href">{{subMenu.label}}</router-link>
                        <a v-else
                           class="menu-link"
                           :href="subMenu.href">{{subMenu.label}}</a>
                    </el-menu-item>
                </el-submenu>
            </el-submenu>
        </el-menu>
        <div class="userName">
            <el-dropdown trigger="click"
                         @command="changePassWord">
                <span class="el-dropdown-link">
                    {{userName}}
                    <i class="el-icon-caret-bottom el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="change">
                        修改密码
                    </el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <span class="signOut"
                  @click="signOut">退出登录</span>
        </div>
        <div class="container">
            <router-view></router-view>
        </div>
        <el-dialog title="修改密码"
                   :visible.sync="dialogPassword"
                   width="600px">
            <div class="dialog-item-password">
                <div class="dialog-left">
                    老密码：
                </div>
                <div class="dialog-right">
                    <el-input v-model="oldPass"
                              type="password"></el-input>
                </div>
            </div>
            <div class="dialog-item-password">
                <div class="dialog-left">
                    新密码：
                </div>
                <div class="dialog-right">
                    <el-input v-model="newPass"
                              type="password"></el-input>
                </div>
            </div>
            <div class="dialog-item-password">
                <div class="dialog-left">
                    重复新密码：
                </div>
                <div class="dialog-right">
                    <el-input v-model="repeatPass"
                              type="password"></el-input>
                </div>
            </div>
            <div class="dialog-bottom">
                <el-button @click="dialogPassword = false">关 闭</el-button>
                <el-button type="primary"
                           @click="savePassword()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

const URL_MAP = {
    '/tag/list': '/tag/list',
    '/crm/userList': '/crm/userList'
};

export default {
    name: 'entry',
    data() {
        return {
            menus: [],
            moreMenus: [],
            title: '',
            userName: 'admin',
            dialogPassword: false,
            oldPass: '',
            newPass: '',
            repeatPass: ''
        };
    },
    computed: {
        ...mapState({
            // 箭头函数可使代码更简练
            menuList: state => state.menu,
            userInfo: state => state.userInfo
        })
    },
    methods: {
        ...mapMutations({
            setMenu: 'setMenu',
            setUserInfo: 'setUserInfo',
            setOperation: 'setOperation'
        }),
        getMenus() {
            this.$get('/index.json').then((res) => {
                this.setUserInfo(res.data.object.user);
                this.setMenu(res.data.object.menuList || []);
                // this.setSupplierList(res.data.object.supplierList);
                this.handleMenu();
            });
        },
        handleMenu() {
            this.healthUrlMap(this.menuList);
            this.menus = this.menuList.splice(0, 5);
            this.moreMenus = this.menuList;
            this.title = this.userInfo.showName;
        },
        healthUrlMap(healthMenu) {
            healthMenu.forEach((item) => {
                if (item.children instanceof Array) {
                    item.children.forEach((subItem) => {
                        if (URL_MAP[subItem.href]) {
                            subItem.href = URL_MAP[subItem.href];
                            subItem.router = true;
                        }
                    });
                }
            });
        },
        // healthUrlMap(healthMenu) {
        //     healthMenu.forEach((item) => {
        //         if (item.children instanceof Array) {
        //             item.children.forEach((subItem) => {
        //                 if (URL_MAP[subItem.href]) {
        //                     subItem.href = URL_MAP[subItem.href];
        //                     subItem.router = true;
        //                 }
        //             });
        //         }
        //     });
        // },
        changePassWord(e) {
            if (e === 'change') {
                this.dialogPassword = true;
            }
        },
        savePassword() {
            const msg = this.checkPassword();
            if (!msg) {
                const password = {
                    oldPass: this.oldPass,
                    newPass: this.newPass,
                };
                this.$post('/updatePassword.json', password).then(() => {
                    this.$message.success('密码修改成功');
                    this.dialogPassword = false;
                    this.oldPass = '';
                    this.newPass = '';
                    this.repeatPass = '';
                });
            } else {
                this.$alert(msg);
            }
        },
        checkPassword() {
            let msg = '';
            if (this.newPass.length < 6) msg = '新密码不能少于6位';
            if (this.newPass !== this.repeatPass) msg = '您两次输入的新密码不一致,请确认';
            if (!this.repeatPass.length) msg = '请输入重复新密码';
            if (!this.newPass.length) msg = '请输入新密码';
            if (!this.oldPass.length) msg = '请输入老密码';
            return msg;
        },
        signOut() {
            this.$get('/logout.json').then(() => window.location = '/login').catch(() => window.location = '/login');
        }
    },
    mounted() {
        this.getMenus();
    }
};
</script>

